import { Component, OnInit } from '@angular/core';
import { ClientService } from '../../../services/client/client.service';
import { NotificationService } from '../../../services/notification/notification.service';
import { ModelService } from '../../../services/model/model.service';

@Component({
  selector: 'app-send-forgot-model',
  templateUrl: './send-forgot-model.component.html',
  styleUrls: ['./send-forgot-model.component.scss']
})
export class SendForgotModelComponent implements OnInit {
  loading: boolean = false;
  email: string = null;
  error: boolean = false;
  constructor(
    private modelService: ModelService,
    private notificationService: NotificationService
  ) { }

  ngOnInit(): void {
  }

  send() {
    if (!this.email) {
      this.error = true;
      return null;
    }
    this.error = false;
    this.loading = true;
    this.modelService.forgot(this.email).subscribe(
      (data: {success: boolean, message: string}) => {
        this.loading = false;
        // console.log(data);
        if (data.success) {
          this.notificationService.infoMsg(data.message);
          this.email = null;
        } else {
          this.notificationService.errorMsg(data.message);
        }
      }, (error) => {
        console.log(error);
        this.loading = false;
      }
    )
  }

}
