import { Component, Input, OnInit } from '@angular/core';
import { PopupService } from '../../../services/popup/popup.service';

@Component({
  selector: 'app-topbar',
  templateUrl: './topbar.component.html',
  styleUrls: ['./topbar.component.scss']
})
export class TopbarComponent implements OnInit {
  @Input()
  context: string;

  @Input()
  visible: boolean;

  @Input()
  minuteur: boolean;

  @Input()
  actif: number = 0;

  @Input()
  hour: number = 0;

  @Input()
  minute: number = 0;

  @Input()
  second: number = 0;

  @Input()
  reinit: boolean; // reinit to 0

  @Input()
  leaved: boolean; // Model Quitter

  user = "assets/icons/friends_soft.svg";
  strech = "assets/icons/full-size.svg";
  microOn = "assets/icons/microphone.svg";
  microOff = "assets/icons/microphone-off.svg";
  mute = "assets/icons/mute.svg";
  video = "assets/icons/video-camera.svg";
  noVideo = "assets/icons/no-video.svg";
  volume ="assets/icons/volume.svg";
  soundOff = "assets/icons/sound-off.svg";
  add = "assets/icons/add.svg";

  isSound: boolean = true;
  isVideo: boolean = true;
  isStreched: boolean = true;
  isMicro: boolean = true;

  currentMinute: number = 0;
  countUser: string = "01";

  showTools = false;

  constructor(
    private popupService: PopupService
  ) {

  }

  ngOnInit(): void {
    this.startMinuteur();
  }

  change (material: string) {
    switch (material) {
      case 'son':
        this.isSound = !this.isSound;
        break;
      case 'micro':
        this.isMicro = !this.isMicro;
        break;
      case 'video':
        this.isVideo = !this.isVideo;
        break;
      case 'strech':
        this.isStreched = !this.isStreched;
        break;
      default:
        break;
    }
  }

  toggleTools() {
    this.showTools = !this.showTools;
  }

  startMinuteur() {
    setInterval(() => {
      if (!this.leaved) {
        if (!this.reinit) {
          this.second++;
          if (this.second > 59) {
            this.second = 0;
            this.minute++;
            if (this.minute > 59) {
              this.minute = 0;
              this.hour++;
            }
          }
        } else {
          this.second = 0;
          this.minute = 0;
          this.hour = 0;
        }
      }
    }, 1000);
  }
}
