import { AuthService } from './../../../services/auth/auth.service';
import { Component, Input, OnInit } from '@angular/core';
import { SlideInOutAnimation } from 'src/app/animation';
import { HeaderService } from 'src/app/services/header/header.service';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmLogoutComponent } from '../../../modals/confirm-logout/confirm-logout/confirm-logout.component';
import { ModelService } from '../../../services/model/model.service';

@Component({
  selector: 'app-connected-model',
  templateUrl: './connected-model.component.html',
  styleUrls: ['./connected-model.component.scss'],
  animations: [SlideInOutAnimation]
})
export class ConnectedModelComponent implements OnInit {
  animationState = 'out';
  chat = "assets/icons/chat-comment.svg";
  settings = "assets/icons/settings.svg";

  @Input()
  actif: string;
  active: number = 0;

  constructor(
    private authSrv: AuthService,
    private router: Router,
    private dialog: MatDialog,
    private modelService: ModelService
    ) { }

  ngOnInit(): void {
    if (this.actif) this.active = parseInt(this.actif);
  }

  toggleShowDiv(divName: string) {
    if (divName === 'contain_connected') {
      console.log(this.animationState);
      this.animationState = this.animationState === 'out' ? 'in' : 'out';
      console.log(this.animationState);
    }
  }

  logout() {
    const dialogRef = this.dialog.open(ConfirmLogoutComponent, {
      width: '50vh',
      data: {action: 'model'}
    });

    dialogRef.afterClosed().subscribe(result => {});
  }

  goToChat() {
    this.modelService.getInfo().subscribe(
      (data) => {
        const status = data.profile.status;
        if (status === 'En live') {
          this.router.navigate(['/modele/live-private'])
        } else if (status === 'En vip') {
          this.router.navigate(['/modele/live-vip'])
        } else {
          this.router.navigate(['/modele/chat'])
        }
      }
    )
  }
}

