import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-live-vip-stat',
  templateUrl: './live-vip-stat.component.html',
  styleUrls: ['./live-vip-stat.component.scss']
})
export class LiveVipStatComponent implements OnInit {
  @Input()
  type_show: any;

  @Input()
  tarif_show: any;

  @Input()
  time_show: any;

  @Input()
  client: any;

  @Input()
  time_passed: any;

  @Input()
  hour_passed: any;

  @Input()
  minute_passed: any;

  @Input()
  second_passed: any;

  @Input()
  actualCredit: any;

  @Input()
  winCredit: any;

  @Input()
  totalCredit: any;


  stats1 = [
    {label: "Type du show", value: "VIP"},
    {label: "Tarif du show", value: "15 Crdits / mn"},
    {label: "Client", value: "Motome"}
  ];

  stats2 = [
    {label: "Solde de crdits", value: "225 Crdits"},
    {label: "Dure passe", value: "00:00"},
    {label: "Crdits encaisss", value: "150 Credits"},
    {label: "Solde total", value: "375 Credits"},
  ];

  constructor() { }

  ngOnInit(): void {
    this.startMinuteur();
  }

  startMinuteur() {
    setInterval(() => {
          this.second_passed++;
          if (this.second_passed > 59) {
            this.second_passed = 0;
            this.minute_passed++;
            if (this.minute_passed > 59) {
              this.minute_passed = 0;
              this.hour_passed++;
            }
          }
    }, 1000);
  }

}
