import { Component, OnInit,
  OnDestroy, PLATFORM_ID,
  Inject, ViewChild,
  ElementRef } from '@angular/core';
import {isPlatformBrowser} from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { Chat, ChatSocket } from 'src/app/interfaces/chat.interface';
import { ChatService } from 'src/app/services/chat/chat.service';
import { ModelService } from 'src/app/services/model/model.service';
import { SocketService } from 'src/app/services/socket/socket.service';
import { PopupService } from '../../../services/popup/popup.service';
import { ProfilService } from '../../../services/profil/profil.service';
import { RoomVipService } from '../../../services/roomVip/room-vip.service';
import { ClientService } from '../../../services/client/client.service';
import { PackModalComponent } from 'src/app/modals/pack-modal/pack-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { TimerService } from '../../../services/timer/timer.service';
import { CreditTimer, TypeTimer } from 'src/app/interfaces/timer.interface';
import { NotificationService } from '../../../services/notification/notification.service';

// Peer
import './../../modele/shim';
import Peer from "peerjs"

@Component({
  selector: 'app-live-vip',
  templateUrl: './live-vip.component.html',
  styleUrls: ['./live-vip.component.scss', './../live.scss']
})
export class LiveVipComponent implements OnInit {
  context: string = "VIP";
  imgPoster = "assets/images/image.png";
  thrombone = "assets/icons/paper-clip.svg";
  send = "assets/icons/send-button.svg";
  dot = "assets/icons/more_purple.svg";
  close = "assets/icons/close.svg";

  toggleDot: boolean = false;

  bg : "assets/logos/dark-light.jpg";
  modelPseudo = '';
  modelId: number;
  idRoom: number;
  actif: number;
  message: string = '';
  chatColor = "black";
  clientId: number;
  clientPseudo: string = '';
  clientCredit: number = 0;
  client = "assets/images/user_chat.png";

  messages: Chat[] = [];
  leaved: boolean = false;

  indisponible: boolean = false;

  timer = {
    id: 0,
    hour: 0,
    minute: 0,
    second: 0,
    fail: false,
    reinit: false
  };

  show = {
    id: 0,
    credit: null,
    second: null,
    type: null
  };

  // @HostListener("window:beforeunload", ["$event"]) unloadHandler(event: Event) {
  //   this.socketService.leaveVip(this.idRoom, 'client');
  // }

  // WEB RTC VIDEO
  @ViewChild('video_live_client', {static: true}) video: ElementRef<HTMLVideoElement>;
  @ViewChild('video_live_model', {static: true}) remote_video: ElementRef<HTMLVideoElement>;

  private peer: Peer;
  peerIdShare: string;
  peerId: string;
  private lazyStream: any;
  currentPeer: any;
  private peerList: Array<any> = [];

  constructor(
    @Inject(PLATFORM_ID) private _platform: Object,
    private router: Router,
    private route: ActivatedRoute,
    private modelService: ModelService,
    private roomVipService: RoomVipService,
    private store: LocalStorageService,
    private chatService: ChatService,
    private socketService: SocketService,
    private popupService: PopupService,
    private profilService: ProfilService,
    private clientService: ClientService,
    private dialog: MatDialog,
    private timerService: TimerService,
    private notificationService: NotificationService
    ) {
      this.peer = new Peer();
      const id = this.route.snapshot.paramMap.get('model');
      this.modelId = parseInt(id);
    }

  ngOnInit(): void {
    if (!this.modelId) {
      this.errorRoom();
      return null;
    }
    this.getModel();
    this.initColor();
    this.clientService.lastRoom(this.router.url);
  }

  async ngOnDestroy() {
    (await this.creditTimer()).subscribe(
      (data) => {
        // console.log("Response credit timer ", data);
      }
    );
    // this.roomVipService.leaveRoom();
    // this.socketService.leaveVip(this.idRoom, 'client');
    // console.log('destroy')
  }

  // Get model infos
  getModel() {
    this.modelService.getModel(this.modelId).subscribe(
        (data) => {
          // console.log('Model ', data);
          this.bg = data.path_soft;
          this.modelPseudo = data.pseudo;
          if (data.profile.status !== 'En vip') {
            this.errorRoom();
            return null;
          }
          this.getMyInfo();
        }
    )
  }

  // Get client infos + get model info
  getMyInfo() {
      this.clientService.getMyInfos().subscribe(
        (data) => {
          // console.log('My INFO ', data);
          this.clientId = data.id;
          this.clientPseudo = data.pseudo;
          this.clientCredit = data.credit ? data.credit.credit : 0; // Client Credit
          this.getInfoRoom();
        }
      )
  }

  getInfoRoom() {
    this.roomVipService.getRoomModel(this.modelId).subscribe(
      (data) => {
        console.log('Model room ', data);
        this.idRoom = data.idRoom;

        if (data.idRoom === null) {
          this.obsolete();
          this.actif = 0;
          this.idRoom = 0;
          return false;
        }

        this.actif = data.actif > 2 ? 2: data.actif;

        if (this.clientId !== data.clientId) {
          this.errorRoom();
          return false;
        }
        this.getMessages();
        this.initSocket();
      }
    )
  }

  // Init the room + Get client infos
  // initRoom() {
  //   const roomVIP = parseInt(this.store.get('roomVIP'));
  //   this.idRoom = roomVIP;
  //   if (!roomVIP) {
  //     this.errorRoom();
  //     return false;
  //   }
  //   this.getMyInfo();
  // }


  getMessages() {
    this.chatService.getMessage(this.idRoom, 'vip').subscribe(
      (data) => {
        this.messages = data;
      }
    )
  }

  initSocket() {
    this.socketService.joinVip(this.idRoom, this.modelId);

    this.socketService.listen(`joined ${this.idRoom}V`).subscribe(
      (data: any) => {
        // console.log('Joined ', data);
        this.actif = data < 0 ? 0 : data;
        this.actif = data > 2 ? 2 : data;
        console.log('Actif ', this.actif);
      }
    );

    this.socketService.listen(`leaved ${this.idRoom}V`).subscribe(
      (data: any) => {
        // console.log('leaved ', data);
        this.actif = data < 0 ? 0 : data;
        this.actif = data > 2 ? 2 : data;
        console.log('Actif ', this.actif);
      }
    );

    this.socketService.listen(`message ${this.idRoom}V`).subscribe(
      (data) => {
        // console.log('Msg to client ', data);
        this.getMessages();
      }
    );

    this.socketService.listen(`model leaved ${this.idRoom}V`).subscribe(
      (data) => {
        this.leaved = true;
        this.indisponible = true;
        this.actif = 1;
        this.popupService.info('/accueil/registered','LIVE INDISPONIBLE',
        `${this.modelPseudo} a ferm le live de ce room`);
      }
    );

    this.initTimer();
    this.initLiveVideo();
  }

  obsolete() {
    this.popupService.info('/accueil/registered','LIVE INDISPONIBLE',
    `Aucun live n\'est lanc dans ce room`);
  }

  initColor() {
    if (this.store.get("ticket_chat")) {
      this.chatColor = this.store.get("ticket_chat");
      return true;
    };
    this.roomVipService.getColor().subscribe(
      (data) => {
        this.chatColor = data.color;
        this.store.set("ticket_chat", data.color);
      }
    )
  }

  sendMessage() {
    if (!this.message) return;
    const data: Chat = {
      idRoom: this.idRoom,
      message: this.message,
      type_source: 'client',
      id_source: this.clientId,
      code_couleur: this.chatColor,
      type_chat: 'vip'
    }
    console.log(data);
    this.chatService.postMessage(data).subscribe(
      (data) => {
        this.getMessages();
        const msg: ChatSocket = {
          room: this.idRoom + 'V',
          role: 'client',
          id: this.clientId,
          message: this.message
        }
        this.socketService.sendMessage(msg);
        this.message = null;
      }
    )
  }

  toggleDots() {
    this.toggleDot = !this.toggleDot;
    console.log(this.toggleDot);
  }


  async liveOut() {
      // this.creditTimer();
      await this.leaveTimer().then(
        (data) => {
          this.roomVipService.leaveRoom();
          this.socketService.leaveVip(this.idRoom, 'client').then(
            () => {
              this.store.remove('roomVIP');
              this.router.navigate(['/accueil/registered']);
            }
          );
        });
        this.onStop();
  }

  async leaveTimer() {
    const data = {
      id: this.timer.id,
      type: TypeTimer.VIP,
      leaved: true,
      push: new Date().toString()
    }
    // console.log("Before leave timer ", data)
    return await this.timerService.updateTimer(data).subscribe(
      (data) => {
        // console.log('Leave timer ', data);
        this.timer.reinit = true;
      }
    )
  }

  buy() {
    const dialogRef = this.dialog.open(PackModalComponent, {
      width: '80vh',
      data: {}
    });
    dialogRef.afterClosed().subscribe(result => {});
  }

  errorRoom() {
    this.popupService.info(null,'LIVE INDISPONIBLE',
    `Ce live n\'est plus disponible`);
    this.indisponible = true;
  }

  async getCredit() {
    return await this.clientService.getCredit().subscribe(
      (data) => {
        this.clientCredit = data.credit;
        console.log('Client credit ', this.clientCredit);
        if (this.show.credit < this.clientCredit) return null;
        this.timer.fail = true;
        // if Not leaved
        if (!this.timer.reinit) {
          this.notificationService.failure("CREDIT INSUFFISANT", "Veuillez vous recharger.");
        }
      }
    )
  }

    // Timer
    async initTimer() {
      this.timerService.getTimer(this.modelId, TypeTimer.VIP).subscribe(
        async (data) => {
          // console.log('Timer ', data);
          const created: Date = data.createdAt;
          const updated: Date = data.updatedAt;
          this.timer.id = data.id;
          // console.log(created.toString(), " to ", updated.toString());
          const {hour, minute, second} = this.timerService.convertTime(created.toString(), updated.toString());
          this.timer.hour = hour;
          this.timer.minute = minute;
          this.timer.second = second;
          // console.log(this.timer.hour, ':', this.timer.minute, ':', this.timer.second);
          await this.getCostShow();
        }
      )
    }

    async getCostShow() {
      return await this.timerService.getCostShow(TypeTimer.VIP).subscribe(
        (data: any) => {
          this.show.id = data.id;
          this.show.credit = data.credit;
          this.show.second = data.second;
          this.show.type = data.type;
          // console.log('Show cost ' , this.show, " - credit - " , this.clientCredit);
          if (this.clientCredit < this.show.credit) {
            this.aucunCredit();
            return null;
          } else if (this.clientCredit <= (this.show.credit * 2)) {
            this.creditInsuffisant();
            return null;
          }
          this.beginTimer();
        }
      )
    }

    creditInsuffisant() {
      this.popupService.info(null,'CREDIT INSUFFISANT',
      `Vous n\'avez presque plus de crdit. Veuillez vous recharger.`);
      setTimeout(
        () => {
          this.getCredit();

        }, 20000
      )
    }

    aucunCredit() {
      this.timer.fail = true;
      this.popupService.info('/achat_de_credit','ACHAT DE CREDIT',
      `Veuillez vous recharger.`);
    }

    async beginTimer() {
      const delay = this.show.second * 1000;
      // console.log(delay, " ms ");
      await (await this.creditTimer()).subscribe( // launch main creditation
        (data: any) => {
          // console.log("Response credit timer ", data);
          this.clientCredit = data.credit ? data.credit : 0;
          if (data.credit <= 0) {
            this.aucunCredit();
            return null;
          } else if (data.credit <= (this.show.credit * 2)) {
            this.creditInsuffisant();
            return null;
          }
        }
      );
      setInterval(
        async () => {
          if (!this.timer.reinit) {
            await (await this.creditTimer()).subscribe(
              (data: any) => {
                // console.log("Response credit timer ", data);
                this.clientCredit = data.credit ? data.credit : 0;
                if (data.credit <= 0) {
                  this.aucunCredit();
                  return null;
                } else if (data.credit <= (this.show.credit * 2)) {
                  this.creditInsuffisant();
                  return null;
                }
              }
            );
          }
        }, delay
      )
    }

    async creditTimer() {
      if ((this.clientId === undefined) || (this.show.credit == null)) return null;
      const data: CreditTimer = {
        clientId: this.clientId,
        modelId: this.modelId,
        timerId: this.timer.id,
        showCredit: this.show.credit,
        showSecond: this.show.second,
        showType: this.show.type,
        roomId: this.idRoom
      }
      // console.log("credit timer post ", data);
      return await this.timerService.creditTimer(data);
    }

  // WEB RTC - LIVE VIDEO
  initLiveVideo() {
    this.onStart();
    this.getPeerId();
  }

  onStart(){
    if(isPlatformBrowser(this._platform) && 'mediaDevices' in navigator) {
      navigator.mediaDevices.getUserMedia({video: true}).then((ms: MediaStream) => {
        const _video = this.video.nativeElement;
        _video.srcObject = ms;
        _video.play();
      });
    }
  }

  onStop() {
    if (this.video) {
      this.video.nativeElement.pause();
      if ((this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0]) {
        (this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0].stop();
      }
      this.video.nativeElement.srcObject = null;
    }
  }

  private getPeerId = () => {
    console.log("Get Peer");
    this.peer.on('open', (id) => {
      console.log("Peer Id ", id);
      this.peerId = id;
      this.socketService.sendClientPeerId({peerId: this.peerId, room: this.idRoom+'V'});
    });

    this.peer.on('call', (call) => {
      this.onStop();
      navigator.mediaDevices.getUserMedia({
        video: true,
        audio: true
      }).then((stream) => {
        this.lazyStream = stream;
        const _video = this.video.nativeElement;
        _video.srcObject = stream;
        _video.play();

        call.answer(stream);
        call.on('stream', (remoteStream) => {
          if (!this.peerList.includes(call.peer)) {
            this.streamRemoteVideo(remoteStream);
            this.currentPeer = call.peerConnection;
            this.peerList.push(call.peer);
          }
        });
      }).catch(err => {
        console.log(err + 'Unable to get media');
      });
    });
  }

  connectWithPeer(): void {
    this.callPeer(this.peerIdShare);
  }

  private callPeer(id: string): void {
    navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true
    }).then((stream) => {
      this.lazyStream = stream;

      const call = this.peer.call(id, stream);
      call.on('stream', (remoteStream) => {
        if (!this.peerList.includes(call.peer)) {
          this.streamRemoteVideo(remoteStream);
          this.currentPeer = call.peerConnection;
          this.peerList.push(call.peer);
        }
      });
    }).catch(err => {
      console.log(err + 'Unable to connect');
    });
  }

  private streamRemoteVideo(stream: any): void {
    const _video = this.remote_video.nativeElement;
    _video.srcObject = stream;
    _video.play();
  }

}
