import { AuthService } from 'src/app/services/auth/auth.service';
import {Component, Inject, OnInit} from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { Router } from '@angular/router';
import { LogService } from '../../../services/log/log.service';
import { RoomService } from '../../../services/room/room.service';
import { LocalStorageService } from 'ngx-localstorage';
import { SocketService } from '../../../services/socket/socket.service';
import { ClientService } from '../../../services/client/client.service';

@Component({
  selector: 'app-confirm-logout',
  templateUrl: './confirm-logout.component.html',
  styleUrls: ['./confirm-logout.component.scss']
})
export class ConfirmLogoutComponent implements OnInit {
  action: string;
  exclamation = "assets/icons/exclamation-mark.svg";
  password: string = "";
  error: boolean = false;
  empty: boolean = false;
  constructor(
    public dialogRef: MatDialogRef<ConfirmLogoutComponent>,
    private authService:AuthService,
    private socketService: SocketService,
    private store: LocalStorageService,
    private roomService: RoomService,
    private logService: LogService,
    private clientService: ClientService,
    @Inject(MAT_DIALOG_DATA) public data) {
      this.action = data.action;
      // console.log("Logout modal " + this.action);
    }

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit(): void {
  }

  async validate(): Promise<void> {
    if (this.action == 'model') {
      await this.logService.createLog('deconnection').subscribe(
        (data) => {
          const room = this.store.get('room');
          this.socketService.leave(parseInt(room), 'model');
          this.store.remove('room');
          this.roomService.leaveRoom();
          this.authService.logoutUser('model', '/modele/connexion');
        }
      )
    }
    else {
      this.clientService.lastRoom(null);
      this.authService.logoutUser('client', '/accueil');
    }
    this.dialogRef.close();
  }
}
