import { Component, Input, OnInit } from '@angular/core';
import { SlideInOutAnimation } from 'src/app/animation';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmLogoutComponent } from 'src/app/modals/confirm-logout/confirm-logout/confirm-logout.component';
import { ClientService } from '../../../services/client/client.service';
import { NotificationService } from '../../../services/notification/notification.service';

@Component({
  selector: 'app-connected',
  templateUrl: './connected.component.html',
  styleUrls: ['./connected.component.scss'],
  animations: [SlideInOutAnimation]
})
export class ConnectedComponent implements OnInit {
  animationState = 'out';
  chat = "assets/icons/chat-comment.svg";
  settings = "assets/icons/settings.svg";

  @Input()
  actif: string;
  active: number = 0;

  constructor(
    private router: Router,
    private dialog: MatDialog,
    private clientService: ClientService,
    private notificationService: NotificationService
    ) { }

  ngOnInit(): void {
    if (this.actif) this.active = parseInt(this.actif);
  }

  toggleShowDiv(divName: string) {
    if (divName === 'contain_connected') {
      console.log(this.animationState);
      this.animationState = this.animationState === 'out' ? 'in' : 'out';
      console.log(this.animationState);
    }
  }

  logout() {
    const dialogRef = this.dialog.open(ConfirmLogoutComponent, {
      width: '50vh',
      data: {action: 'client'}
    });
    dialogRef.afterClosed().subscribe(result => {});
  }

  lastChat() {
    const lastRoom = this.clientService.lastRoom();
    if (!lastRoom) {
      this.notificationService.infoMsg("Aucun rcent Chat");
      return null;
    }
    // console.log(lastRoom);
    this.router.navigateByUrl(lastRoom);
  }
}
