import { Component, OnInit } from '@angular/core';
import { cloneDeep } from 'lodash';
import * as agCharts from 'ag-charts-community';
import { log } from './connexion';
@Component({
  selector: 'app-connexion',
  templateUrl: './connexion.component.html',
  styleUrls: ['./connexion.component.scss']
})
export class ConnexionComponent implements OnInit {
  options: any;
  updating: boolean = false;
  history = [
    // { year: '', lundi: 0, mardi: 0, mercredi: 0, jeudi: 0, vendredi: 0, samedi: 0, dimanche: 0},
    { year: '2014', lundi: 18, mardi: 20, mercredi: 10, jeudi: 10, vendredi: 8, samedi: 10, dimanche: 6}
  ];

  constructor() {
    this.options = {
      autoSize: true,
      data: this.getData(),
      series: [
        {
          type: 'column',
          xKey: 'year',
          yKeys: ['lundi', 'mardi', 'mercredi', 'jeudi', 'vendredi', 'samedi', 'dimanche'],
          yNames: ['Lundi', 'Mardi', 'Mercredi', 'Jeudi', 'Vendredi', 'Samedi', 'Dimanche'],
          grouped: true,
          fills: ['#117abe', 'rgb(247, 113, 4)', 'rgb(117, 117, 116)', 'rgb(255, 166, 0)',
          'rgb(0, 0, 255)', 'rgb(0, 128, 0)', 'rgb(0, 0, 85)'
          ],
          strokeWidth: 0,
          marker: {
            enabled: false,
            fill: '#80a0c3',
          },
        },
      ],
      axes: [
        {
          type: 'category',
          position: 'bottom',
          title: {
            enabled: true,
            text: 'Nombre d\'heure de connexion',
            color: 'white'
          },
          tick: { color:  "rgb(255, 255, 255)"},
          label: {
            color:  "rgb(255, 255, 255)",
            formatter: function (params) {
              return '';
            },
          },
        },
        {
          type: 'number',
          position: 'left',
          keys: ['lundi', 'mardi', 'mercredi', 'jeudi', 'vendredi', 'samedi', 'dimanche'],
          title: {
            enabled: false
          },
          label: {
            color:  "rgb(255, 255, 255)",
            formatter: function (params) {
              return params.value;
            },
          },
        }
      ],
      background: {
        fill: "#FFF",
        visible: false
      },
      tooltip: {
        enabled: true
      },
      navigator: {
        enabled: true
      },
      title: { text: '' },
      legend: {
        position: 'bottom',
        enabled: true,
        item: {
          label: {
            color: 'white'
          }
        }
      }
    };
  }

  ngOnInit(): void {
    this.startUpdates();
    this.useDefaultGridStyle();
  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    console.log("destroyed");
  }

  useDefaultGridStyle = () => {
    const options = cloneDeep(this.options);

    var gridStyle = [
      {
        stroke: 'white',
        lineDash: [2, 1],
      },
    ];
    options.axes[0].gridStyle = gridStyle;
    options.axes[1].gridStyle = gridStyle;
    this.options = options;
  };

  update = () => {
    const options = cloneDeep(this.options);
    options.data = this.getData();
    this.options = options;
  };

  startUpdates = () => {
    if (this.updating) {
      return;
    }
    this.updating = true;
    this.update();
    // setInterval(this.update, 500);
  };

  getData() {
    this.updating = false;
    return this.history;
  }
}

