import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { LocalStorageService } from 'ngx-localstorage';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class ClientService {
  endpoint = environment.endpoint

  constructor(
    private http: HttpClient,
    private store: LocalStorageService
  ) { }

  getMyInfos(): Observable<any> {
    return this.http.get(`${this.endpoint}/client`, httpOptions)
  }

  getClient(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/client/${id}`, httpOptions)
  }

  getCredit(): Observable<any> {
    return this.http.get(`${this.endpoint}/credit/client`, httpOptions)
  }

  forgot(email : string) : Observable<any>{
    const options = {
      email : email
    }
    return this.http.post(`${this.endpoint}/client/forgot`, options, httpOptions)
  }

  reinitPassword(userId: number, password: string) : Observable<any>{
    const options = {
      id : userId,
      newPassword: password
    }
    return this.http.post(`${this.endpoint}/client/reinitPassword`, options, httpOptions)
  }

  lastRoom(linkRoom?: string) {
    if (linkRoom === null) return this.store.remove('lastRoom');
    if (linkRoom)
      this.store.set("lastRoom", linkRoom);
    return this.store.get("lastRoom");
  }

  changePseudo(pseudo: string) {
    const data = {
      pseudo
    }
    return this.http.post(`${this.endpoint}/client/changePseudo`, data, httpOptions)
  }

  changePassword(oldPassword: string, newPassword: string) {
    const data = {
      oldPassword,
      newPassword
    }
    return this.http.post(`${this.endpoint}/client/changePassword`, data, httpOptions)
  }

  deleteAccount(password: string) {
    const data = {
      password
    }
    return this.http.post(`${this.endpoint}/client/deleteAccount`, data, httpOptions)
  }

  deactivateAccount(password: string) {
    const data = {
      password
    }
    return this.http.post(`${this.endpoint}/client/deactivateAccount`, data, httpOptions)
  }

  reactivateAccount() {
    return this.http.post(`${this.endpoint}/client/reactivateAccount`, {}, httpOptions)
  }

  restoreAccount() {
    return this.http.post(`${this.endpoint}/client/restoreAccount`, {}, httpOptions)
  }
}
