import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Information } from '../../interfaces/model-information.interface';
import { Setting } from '../../interfaces/setting.interface';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class ProfilService {
  endpoint = environment.endpoint

  constructor(
    private http: HttpClient
  ) { }

  getProfil(): Observable<any> {
    return this.http.get(`${this.endpoint}/profile`, httpOptions)
  }

  getSpecificProfil(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/profile/${id}`, httpOptions)
  }

  createProfil(id: number): Observable<any> {
    return this.http.post(`${this.endpoint}/profile/create/${id}`,{id}, httpOptions)
  }

  updateProfil(profile: Information) {
    return this.http.patch(`${this.endpoint}/profile/${profile.id}`, profile, httpOptions)
  }

  updateStatus(modelId: number, status: string) {
    const data = {
      status
    }
    return this.http.patch(`${this.endpoint}/profile/${modelId}`, data, httpOptions)
  }

  getInfo(): Observable<any> {
    return this.http.get(`${this.endpoint}/setting/info`, httpOptions)
  }

  getSetting(): Observable<any> {
    return this.http.get(`${this.endpoint}/setting`, httpOptions)
  }

  updateSetting(setting: Setting) {
    return this.http.patch(`${this.endpoint}/setting/${setting.id}`, setting, httpOptions)
  }

  updateInfo(info: any) {
    return this.http.patch(`${this.endpoint}/setting/model`, info, httpOptions)
  }

  createSetting(id: number): Observable<any> {
    return this.http.post(`${this.endpoint}/setting/create/${id}`,{id}, httpOptions)
  }

  updatePhoto(formData: FormData): Observable<any> {
    return this.http.post(`${this.endpoint}/upload/profile`, formData, {
        reportProgress: true,
        observe: 'events'
    });
  }

  changePassword(data: any): Observable<any> {
    return this.http.post(`${this.endpoint}/setting/password/`,data, httpOptions)
  }

  getCredential(): Observable<any> {
    return this.http.get(`${this.endpoint}/profile/credential`, httpOptions)
  }
}
