import { ChangeDetectionStrategy, Component, OnDestroy, OnInit } from '@angular/core';
import { GalleryItem, Gallery, ImageItem, ImageSize, ThumbnailsPosition } from 'ng-gallery';
import { Lightbox } from 'ng-gallery/lightbox';
import { ModelService } from 'src/app/services/model/model.service';
import { addAlbum } from '../../../interfaces/add-album.interface';
import { AlbumService } from '../../../services/album/album.service';

const data = [
  {
    path_album: 'https://res.cloudinary.com/team-stenny/image/upload/v1627759167/light_fumemz.jpg',
  }
];

@Component({
  selector: 'app-home-live',
  templateUrl: './home-live.component.html',
  styleUrls: ['./home-live.component.scss'],
})
export class HomeLiveComponent implements OnInit {
  mainModel = {
    path_soft: null,
    pseudo: '',
    id: 0
  };

  allLive = [];
  other = [];

  currentAlbum : addAlbum[] = [];

  items: GalleryItem[];

  imageData = data;

  constructor(
    private modelSrv: ModelService,
    private albumService: AlbumService,
    public gallery: Gallery,
    public lightbox: Lightbox
  ) {
  }

  ngOnInit(): void {
    this.init();
    this.getLive();
    this.getNoLive();
  }


  init() {
    // Creat gallery items
    this.items = this.imageData.map(item => new ImageItem({ src: item.path_album, thumb: item.path_album }));
    /** Lightbox Example */
    // Get a lightbox gallery ref
    const lightboxRef = this.gallery.ref('lightbox');

    // Add custom gallery config to the lightbox (optional)
    lightboxRef.setConfig({
      imageSize: ImageSize.Cover,
      thumbPosition: ThumbnailsPosition.Top
    });
    // Load items into the lightbox gallery ref
    lightboxRef.load(this.items);
  }

  getLive() {
    this.modelSrv.getLive().subscribe(
      (data) => {
        this.allLive = data;
        this.mainModel = this.allLive[0];
        // console.log("Live ", this.allLive);
      }
    )
  }

  getNoLive() {
    this.modelSrv.getNoLive().subscribe(
      (data) => {
        this.other = data;
        // console.log("NoLive ", this.other);
      }
    )
  }

  seeModel(modelId: number, path_soft: string) {
    const data = {
      modelId,
      type_album: 'free'
    }
    this.albumService.getModelAlbums(data).subscribe(
      (data: any) => {
        this.imageData = [];
        const path_album = {
          path_album : path_soft
        }

        this.imageData = data;
        this.imageData.push(path_album);
        // console.log("Image dada ", this.imageData);
        this.init();
      }
    )
  }
}
