import { Injectable } from '@angular/core';
import {NgxNotificationDirection, NgxNotificationMsgService, NgxNotificationStatusMsg} from 'ngx-notification-msg'
import { MatSnackBar } from '@angular/material/snack-bar';

@Injectable({
  providedIn: 'root'
})
export class NotificationService {

  constructor(
    private _snackbar: MatSnackBar,
    private readonly ngxNotificationMsgService: NgxNotificationMsgService) {
    }

  success (title: string, message: string) {
    this.ngxNotificationMsgService.open({
      status: NgxNotificationStatusMsg.SUCCESS,
      header: title,
      messages: [message],
      displayIcon: true,
      closeable: true,
      direction: NgxNotificationDirection.TOP_RIGHT,
      displayProgressBar: false,
      delay: 2000
    });
  }

  info (title: string, message: string) {
    this.ngxNotificationMsgService.open({
      status: NgxNotificationStatusMsg.INFO,
      header: title,
      messages: [message],
      displayIcon: true,
      closeable: true,
      direction: NgxNotificationDirection.TOP_RIGHT,
      displayProgressBar: false,
      delay: 2000
    });
  }

  failure (title: string, message: string) {
    this.ngxNotificationMsgService.open({
      status: NgxNotificationStatusMsg.FAILURE,
      header: title,
      messages: [message],
      displayIcon: true,
      closeable: true,
      direction: NgxNotificationDirection.TOP_RIGHT,
      displayProgressBar: false,
      delay: 2000
    });
  }

  simple (title: string, message: string) {
    this.ngxNotificationMsgService.open({
      status: NgxNotificationStatusMsg.NONE,
      header: title,
      messages: [message],
      displayIcon: true,
      closeable: true,
      direction: NgxNotificationDirection.TOP_RIGHT,
      displayProgressBar: false,
      delay: 2000
    });
  }

  errorMsg(message: string) {
    this._snackbar.open(message, "", {
      duration: 2000,
      horizontalPosition: "right",
      verticalPosition: "bottom",
      panelClass: ["erreur_snackar"]
  })
  }

  successMsg(message: string) {
    this._snackbar.open(message, "", {
      duration: 2000,
      horizontalPosition: "right",
      verticalPosition: "bottom",
      panelClass: ["success_snackar"]
  })
  }

  infoMsg(message: string) {
    this._snackbar.open(message, "", {
      duration: 2000,
      horizontalPosition: "right",
      verticalPosition: "bottom",
      panelClass: ["info_snackar"]
  })
  }

}
