import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { LocalStorageService } from 'ngx-localstorage';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { TypeTimer, CreditTimer } from '../../interfaces/timer.interface';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class TimerService {
  endpoint = environment.endpoint

  constructor(
    private http: HttpClient,
    private store: LocalStorageService
  ) { }

  convertTime(created: string, updated: string) {
    let step = 0; let hour = 0; let minute = 0; let second = 0;

    let hourCreated = parseInt(created.substring(11, 13));
    let hourUpdated = parseInt(updated.substring(11, 13));

    let minCreated = parseInt(created.substring(14, 16));
    let minUpdated = parseInt(updated.substring(14, 16));

    let secCreated = parseInt(created.substring(17, 19));
    let secUpdated = parseInt(updated.substring(17, 19));

    while (hourCreated < hourUpdated) {
      secCreated++;
      if (secCreated > 59) {minCreated++; secCreated = 0;}
      if (minCreated > 59) {hourCreated++; minCreated = 0;}
      step++;
    }

    while (minCreated < minUpdated) {
      secCreated++;
      if (secCreated > 59) {minCreated++; secCreated = 0;}
      step++;
    }

    while (secCreated < secUpdated) {
      secCreated++; step++;
    }
    // console.log("Step ", step);

    hour = Math.floor(step / 3600);
    minute = Math.floor((step % 3600) / 60);
    second = (step % 3600) % 60;

    return {hour, minute, second}
  }

  getTimer(modelId: number, type: TypeTimer): Observable<any> {
    const data = {
      modelId: modelId,
      type: type
    }
    return this.http.post(`${this.endpoint}/timer`,data, httpOptions)
  }

  getCostShow(type: TypeTimer) {
    return this.http.get(`${this.endpoint}/cost-show/${type}`, httpOptions)
  }

  updateTimer(data) {
    return this.http.post(`${this.endpoint}/timer/update`, data, httpOptions)
  }

  creditTimer(data: CreditTimer) {
    // console.log(data);
    return this.http.post(`${this.endpoint}/timer/credit`, data, httpOptions)
  }

  beginTimerModel(modelId: number, type: TypeTimer): Observable<any> {
    const data = {
      modelId: modelId,
      type: type
    }
    return this.http.post(`${this.endpoint}/timer/beginTimerModel`,data, httpOptions)
  }

}
