import { Component, OnInit } from '@angular/core';
import { cloneDeep } from 'lodash';
import { ModelService } from '../../../../services/model/model.service';
import { RoomVipService } from '../../../../services/roomVip/room-vip.service';
import { RoomPrivateService } from '../../../../services/roomPrivate/room-private.service';

interface Gain {
  date: string,
  gainVIP: number,
  gainPrivate: number
}

@Component({
  selector: 'app-gain',
  templateUrl: './gain.component.html',
  styleUrls: ['./gain.component.scss']
})
export class GainComponent implements OnInit {
  options: any;
  updating: boolean = false;
  gain: Gain[] = [
    {date:  "01/01/2020", gainVIP: 50, gainPrivate: 50},
    {date:  "02/01/2020", gainVIP: 70, gainPrivate: 10},
    {date:  "03/01/2020", gainVIP: 30, gainPrivate: 120},
    {date:  "04/01/2020", gainVIP: 100, gainPrivate: 100},
    {date:  "05/01/2020", gainVIP: 120, gainPrivate: 50},
    {date:  "06/01/2020", gainVIP: 50, gainPrivate: 90},
    {date:  "07/01/2020", gainVIP: 80, gainPrivate: 150},
  ];

  info = {
    modelId: 0,
    modelPseudo: null
  }

  constructor(
    private modelService: ModelService,
    private roomVIPService: RoomVipService,
    private roomPrivateService: RoomPrivateService
  ) {
    this.options = {
      autoSize: true,
      data: this.getData(),
      series: [
        {
          xKey: 'date',
          yKey: 'gainVIP',
          yName: 'Gain VIP',
          stroke: '#FB77FC',
          marker: {
            size: 2,
            fill: '#FB77FC',
            stroke: '#FB77FC',
          },
        },
        {
          xKey: 'date',
          yKey: 'gainPrivate',
          yName: 'Gain Prive',
          stroke: '#4E009F',
          marker: {
            size: 2,
            fill: '#4E009F',
            stroke: '#4E009F',
            color: '#fff'
          },
        }
      ],
      axes: [
        {
          type: 'category',
          position: 'bottom',
          nice: false,
          // tick: { count: agCharts.time.second.every(3), color:  "rgb(255, 255, 255)"},
          tick: { count: 0,color:  "rgb(255, 255, 255)"},
          label: { format: '%m/%d/%Y', color:  "rgb(255, 255, 255)" },
        },
        {
          type: 'number',
          position: 'left',
          label: { format: '#{.0f}', color:  "#fff"}
        }
      ],
      background: {
        fill: "#FFF",
        visible: false
      },
      tooltip: {
        enabled: true
      },
      navigator: {
        enabled: true
      },
      title: { text: '' },
      legend: {
        position: 'bottom',
        enabled: true,
        item: {
          label: {
            color: 'white'
          }
        }
      }
    };
  }

  ngOnInit(): void {
    this.getInfoModel();
    this.startUpdates();
    this.useDefaultGridStyle();
  }

  useDefaultGridStyle = () => {
    const options = cloneDeep(this.options);

    var gridStyle = [
      {
        stroke: 'white',
        lineDash: [2, 1],
      },
    ];
    options.axes[0].gridStyle = gridStyle;
    options.axes[1].gridStyle = gridStyle;

    this.options = options;
  };

  update = () => {
    const options = cloneDeep(this.options);

    options.data = this.getData();

    this.options = options;
  };

  startUpdates = () => {
    if (this.updating) {
      return;
    }
    this.updating = true;
    this.update();
    setInterval(this.update, 2000);
  };

  getData() {
    this.updating = false;
    return this.gain;
  }

  getInfoModel() {
    this.modelService.getInfo().subscribe(
      (data) => {
        // console.log(data);
        this.info.modelId = data.id;
        this.info.modelPseudo = data.pseudo;
        this.gain = [];
        this.getGainVip(data.id);
        this.getGainPrivate(data.id);
      }
    )
  }

  getGainVip(id: number) {
    this.roomVIPService.getGain(id).subscribe(
      (data: [{createdAt: any, gain: number}]) => {
        // console.log("Gain VIP ", data);
        const result = data;
        result.forEach(
          (gain) => {
            const gainTmp: Gain = {
              date: this.getDate(gain.createdAt),
              gainVIP: gain.gain,
              gainPrivate: 0
            }
            this.gain.push(gainTmp);
          }
        )
        console.log(this.gain);
      }
    )
  }

  getGainPrivate(id: number) {
    this.roomPrivateService.getGain(id).subscribe(
      (data: [{createdAt: any, gain: number}]) => {
        // console.log("Gain Private ", data);
        const result = data;
        result.forEach(
          (gain) => {
            const gainTmp: Gain = {
              date: this.getDate(gain.createdAt),
              gainVIP: 0,
              gainPrivate: gain.gain
            }
            this.gain.push(gainTmp);
          }
        )
        console.log(this.gain);
      }
    )
  }

  getDate(date: string) {
    let year = date.substring(0, 4);
    let month = date.substring(5, 7);
    let day = date.substring(8, 10);
    const result = day+'/'+month+'/'+year;
    return result;
  }
}

