import { Component, OnInit } from '@angular/core';
import { albumFree, albumPrivate, albumVIP } from '../albums/list-album';
import { AlbumService } from '../../../services/album/album.service';
import { Router } from '@angular/router';
import { addAlbum } from 'src/app/interfaces/add-album.interface';
import { ProfilService } from '../../../services/profil/profil.service';

interface Profil {
  pseudo: string,
  path_soft: string | null,
  age: number,
  sex_orientation: string,
  like: string,
  dislike: string,
  status: string,
  social_network: string,
  description: string,
  date_last_connection: string
}

@Component({
  selector: 'app-profil-model',
  templateUrl: './profil-model.component.html',
  styleUrls: ['./profil-model.component.scss']
})
export class ProfilModelComponent implements OnInit {
  bg = "assets/images/bg.jpg";
  camera = "assets/icons/photoAdd.svg";
  profil = "assets/images/image.png";
  sectionAlbum: number = 3;
  bannier = "assets/logos/dark-light.jpg";

  totalAlbumPrivate: number  = 0;
  pagePrivate: number = 0;
  currentAlbumPrivate: addAlbum[] = [];

  totalAlbumVIP: number  = 0;
  pageVIP: number = 0;
  currentAlbumVIP: addAlbum[] = [];


  totalAlbumFree: number  = 0;
  pageFree: number = 0;
  currentAlbumFree: addAlbum[] = [];

  model: Profil =  {
    pseudo: '',
    path_soft: null,
    age: 0,
    sex_orientation: '',
    like: '',
    dislike: '',
    status: '',
    social_network: '',
    description: '',
    date_last_connection: ''
  }

  horaire = [
    {day: 'LUNDI', value: '08h  10h / 18h  21h'},
    {day: 'MARDI', value: '08h  10h / 18h  21h'},
    {day: 'MERCREDI', value: '08h  10h / 18h  21h'},
    {day: 'JEUDI', value: '08h  10h / 18h  21h'},
    {day: 'VENDREDI', value: '08h  10h / 18h  21h'},
    {day: 'SAMEDI', value: '08h  10h / 18h  21h'},
    {day: 'DIMANCHE', value: '08h  10h / 18h  21h'}
  ];

  arrowLeft = "assets/icons/exclamation-mark.svg";
  arrowRight = "assets/icons/exclamation-mark.svg";

  count = {
    "vip": 0,
    "private": 0,
    "free": 0
  }

  constructor(
    private albumService: AlbumService,
    private router: Router,
    private profileService: ProfilService
  ) {
   }

  async ngOnInit() {
    this.init();
    this.getStatus()
    await this.getInfo();
  }

  async getInfo() {
    return await this.profileService.getProfil().subscribe(
      async (data) => {
        this.model = data;
        return await this.profileService.getInfo().subscribe(
          (data) => {
            // console.log(data);
            const date = new Date().getFullYear();
            this.model.age = date - data.year_birth;
            this.model.pseudo = data.pseudo;
            this.model.path_soft = data.path_soft;
            this.model.date_last_connection = this.getDate(this.model.date_last_connection);
          }
        )
      }
    );
  }

  getStatus() {
    this.profileService.getProfil().subscribe(
      (data) => {
        this.model.status = data.status;
      }
    )
  }

  getDate(date: string) {
    if (!date) return null;
    const year = date.substring(0, 4);
    const month = date.substring(5, 7);
    const day = date.substring(8, 10);
    const hour = date.substring(11, 16);
    const full = day + '/' + month + '/' + year + '  ' + hour;
    return full;
  }

  async countAlbum() {
    this.albumService.getCount().subscribe(
      (data) => {
        this.count = data;
          this.totalAlbumPrivate = this.count.private;
          this.totalAlbumVIP = this.count.vip;
          this.totalAlbumFree = this.count.free;
      }
    )
  }

  async init() {
    await this.countAlbum();
    await (await this.albumService.getAlbums('private', 0, 3)).subscribe(
      (data) => {
        this.currentAlbumPrivate = data;
      }
    );

    await (await this.albumService.getAlbums('free', 0, 3)).subscribe(
      (data) => {
        this.currentAlbumFree = data;
      }
    );

    await (await this.albumService.getAlbums('vip', 0, 3)).subscribe(
      (data) => {
        this.currentAlbumVIP = data;
      }
    );
  }

  scrollTo(section: string) {
    document.querySelector(`#${section}`).scrollIntoView();
  }

  toLeft(context: string) {
    switch (context) {
      case 'private':
        if (this.pagePrivate > 0) {
          this.pagePrivate = this.pagePrivate - 1;
          this.fetchAlbum(context, this.pagePrivate * this.sectionAlbum)
        }
        break;

      case 'vip':
        if (this.pageVIP > 0) {
          this.pageVIP = this.pageVIP - 1;
          this.fetchAlbum(context, this.pageVIP * this.sectionAlbum)
        }
        break;

      case 'free':
        if (this.pageFree > 0) {
          this.pageFree = this.pageFree - 1;
          this.fetchAlbum(context, this.pageFree * this.sectionAlbum)
        }
        break;

      default:
        break;
    }
  }

  toRight(context: string) {
    switch (context) {
      case 'private':
        this.pagePrivate =  this.pagePrivate + 1;
        var marge = Math.floor((this.totalAlbumPrivate/this.sectionAlbum) - 0.1); // - 0.1 avoid just division result
        if (this.pagePrivate <= marge) {
          this.fetchAlbum(context, this.pagePrivate * this.sectionAlbum);
        } else { this.pagePrivate = this.pagePrivate -1;}
        break;

      case 'vip':
        this.pageVIP =  this.pageVIP + 1;
        var marge = Math.floor((this.totalAlbumVIP/this.sectionAlbum) - 0.1); // - 0.1 avoid just division result
        if (this.pageVIP <= marge) {
          this.fetchAlbum(context, this.pageVIP * this.sectionAlbum);
        } else { this.pageVIP = this.pageVIP -1;}
        break;

      case 'free':
        this.pageFree =  this.pageFree + 1;
        var marge = Math.floor((this.totalAlbumFree/this.sectionAlbum) - 0.1); // - 0.1 avoid just division result
        if (this.pageFree <= marge) {
          this.fetchAlbum(context, this.pageFree * this.sectionAlbum);
        } else { this.pageFree = this.pageFree -1;}
        break;

      default:
        break;
    }
  }

  async fetchAlbum(context: string, debut: number) {
    switch (context) {
      case 'private':
        await (await this.albumService.getAlbums(context, debut, this.sectionAlbum)).subscribe(
          (data) => {
            this.currentAlbumPrivate = data;
          }
        );
        break;
      case 'vip':
        await (await this.albumService.getAlbums(context, debut, this.sectionAlbum)).subscribe(
          (data) => {
            this.currentAlbumVIP = data;
          }
        );
        break;
      case 'free':
        await (await this.albumService.getAlbums(context, debut, this.sectionAlbum)).subscribe(
          (data) => {
            this.currentAlbumFree = data;
          }
        );
        break;
      default:
        break;
    }
  }

  goToDashboard() {
    this.router.navigate(['/modele/dashboard']);
  }

}
