import {Component, Inject, OnInit} from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { ClientService } from '../../../../services/client/client.service';
import { PopupService } from '../../../../services/popup/popup.service';
import { AuthService } from '../../../../services/auth/auth.service';

@Component({
  selector: 'app-delete',
  templateUrl: './delete.component.html',
  styleUrls: ['./delete.component.scss']
})
export class DeleteComponent implements OnInit {
  action: any;
  exclamation = "assets/icons/exclamation-mark.svg";
  password: string = "";
  error: boolean = false;
  empty: boolean = false;
  constructor(
    public dialogRef: MatDialogRef<DeleteComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private clientService: ClientService,
    private popupService: PopupService,
    private authService: AuthService) {
      this.action = data.action;
    }

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit(): void {
    // console.log(this.action);
  }

  async validate(): Promise<void> {
    this.empty = false;
    this.error = false;
    if (this.password == "") {
        this.empty = true;
        return null;
    }
    if (this.action === 'suppression') {
      this.clientService.deleteAccount(this.password).subscribe(
        (data: any) => {
          // console.log(data);
          if (data.error) {
            this.error = true;
          } else {
            this.popupService.info('client','Suppression',
            `Un email de confirmation vous est envoy pour motif :
            <span style="font-style: italic; color: var(--pink)">Suppression de compte</span>`);
            this.onNoClick();
            this.authService.logoutUser('client', '/accueil');
          }
        }
      )
    } else if (this.action === 'dsactivation') {
      this.clientService.deactivateAccount(this.password).subscribe(
        (data: any) => {
          // console.log(data);
          if (data.error) {
            this.error = true;
          } else {
            this.popupService.info('client','Dsactivation',
            `Un email de confirmation vous est envoy pour motif :
            <span style="font-style: italic; color: var(--pink)">Dsactivation de compte</span>`);
            this.onNoClick();
            this.authService.logoutUser('client', '/accueil');
          }
        }
      )
    }
  }
}
