import { Component } from '@angular/core';
import { Router, Event, NavigationStart, NavigationEnd, NavigationError } from '@angular/router';import { HeaderService } from './services/header/header.service';
import { routeTransitionAnimations } from './utils/slide';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations : [routeTransitionAnimations]
})
export class AppComponent {
  title = 'betweenUs';
  hide: boolean = false;
  unselect: boolean = false;

  constructor(private headerSrv: HeaderService, private router: Router) {
    this.router.events.subscribe((event: Event) => {
      if (event instanceof NavigationStart) {
          // Show loading indicator
      }

      if (event instanceof NavigationEnd) {
          // Hide loading indicator
      }

      if (event instanceof NavigationError) {
          // Hide loading indicator

          // Present error to user
          console.log(event.error);
      }
  });

    this.headerSrv.hidded.subscribe(
      (data) => {
        this.hide = data;
      }
    );
    this.headerSrv.unselected.subscribe(
      (data) => {
        this.unselect = data;
      }
    );
  }


}
