import { HttpEventType } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { Setting } from '../../../../interfaces/setting.interface';
import { ProfilService } from '../../../../services/profil/profil.service';
import { NotificationService } from '../../../../services/notification/notification.service';

@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styleUrls: ['./setting.component.scss']
})
export class SettingComponent implements OnInit {
  colorSwitch = "#FB77FC";
  checkedSound: boolean = true;
  checkedMail: boolean = true;
  checkedMessage: boolean = true;

  originalSetting: Setting;
  setting: Setting = {
    id: 0,
    sound_message: 0,
    sound_notification: 0,
    mail_notification: 0
  }

  file: File;
  formData = new FormData();

  originalInfo: any;
  info = {
    pseudo: '',
    email: '',
    path_soft: null,
    cin: '',
    date_birth: ''
  }

  photoImg = "assets/icons/photoAdd.svg";
  constructor(
    private profileService: ProfilService,
    private notificationService: NotificationService
  ) { }

  ngOnInit(): void {
    this.getInfo();
  }

  getInfo() {
    this.profileService.getInfo().subscribe(
      (data: any) => {
        // console.log(data);
        this.info = data;
        this.originalInfo = {...this.info};
        this.setting = data.setting;
        this.originalSetting = {...this.setting};
      }
    )
  }

  switchSound(event) {
    this.checkedSound = !this.checkedSound;
    // console.log(this.checkedSound);
    if (this.checkedSound)
      this.setting.sound_notification = 1;
    else
      this.setting.sound_notification = 0;
  }

  switchMail(event) {
    this.checkedMail = !this.checkedMail;
    // console.log(this.checkedMail);
    if (this.checkedMail)
      this.setting.mail_notification = 1;
    else
      this.setting.mail_notification = 0;
  }

  switchMessage(event) {
    this.checkedMessage = !this.checkedMessage;
    // console.log(this.checkedMessage);
    if (this.checkedMessage)
      this.setting.sound_message = 1;
    else
      this.setting.sound_message = 0;
  }

  profilChoose(fileInput) {
    this.file =  fileInput.target.files[0];
    var mimeType: any;
    var reader = new FileReader();
    mimeType = this.file.type;
    if (!(mimeType === "image/png" || mimeType === "image/jpeg")) return false;
    reader = new FileReader();
    reader.readAsDataURL(this.file);
    reader.onload = (event) => {
      this.info.path_soft = reader.result;
      // this.nameRecto = this.fileRecto.name;
    };

    this.formData.delete('file');
    this.formData.append('file', this.file, this.file.name);
    this.profileService.updatePhoto(this.formData).subscribe(
      (events) => {
        if (events.type === HttpEventType.UploadProgress) {
          // this.fileUploadProgress = Math.round(events.loaded / events.total * 100) + '%';
        }
        else if (events.type === HttpEventType.Response) {
          const path  = events.body.path;
          this.info.path_soft = path;
        }
      }, (error) => {
        console.log(error);
      }
    )
  }

  async saveSetting() {
    this.profileService.updateSetting(this.setting).subscribe(
      (data) => {
        this.profileService.updateInfo(this.info).subscribe(
          (data) => {
              this.notificationService.success("Modification", "Termin avec succs")
          }
        )
      }
    );
  }

  restoreSetting() {
    this.info.path_soft = this.originalInfo.path_soft;
    this.setting.sound_notification = this.originalSetting.sound_notification;
    this.setting.sound_message = this.originalSetting.sound_message;
    this.setting.mail_notification = this.originalSetting.mail_notification;
  }

}
