import { Component, OnInit } from '@angular/core';
import { Information } from 'src/app/interfaces/model-information.interface';
import { ProfilService } from '../../../../services/profil/profil.service';
import { NotificationService } from '../../../../services/notification/notification.service';


@Component({
  selector: 'app-information',
  templateUrl: './information.component.html',
  styleUrls: ['./information.component.scss']
})
export class InformationComponent implements OnInit {
  information: Information = {
    id: 0,
    like: '',
    dislike: '',
    social_network: '',
    sex_orientation: '',
    description: ''
  };

  oldInfo: Information;

  constructor(
    private profilService: ProfilService,
    private notificationService: NotificationService
  ) {}

  ngOnInit(): void {
    this.getInfo();
  }

  saveInfo() {
    this.profilService.updateProfil(this.information).subscribe(
      (data: any) => {
        this.notificationService.success("Modification", "Termin avec succs")
        this.getInfo();
      }, (error) => {
        console.log(error);
      }
    );
  }

  getInfo() {
    this.profilService.getProfil().subscribe(
      (data) => {
        this.information = data;
        this.oldInfo = {...this.information};
        // console.log(this.information);
      }
    )
  }

  restoreInfo() {
    console.log(this.oldInfo.description);
    this.information.description = this.oldInfo.description;
    this.information.like = this.oldInfo.like;
    this.information.dislike = this.oldInfo.dislike;
    this.information.social_network = this.oldInfo.social_network;
    this.information.sex_orientation = this.oldInfo.sex_orientation;
  }


}
