import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Chat } from '../../interfaces/chat.interface';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};


@Injectable({
  providedIn: 'root'
})
export class ChatService {
  endpoint = environment.endpoint

  constructor(
    private http: HttpClient
  ) { }

  postMessage(message: Chat): Observable<any> {
    return this.http.post(`${this.endpoint}/chat`, message, httpOptions)
  }

  getMessage(idRoom: number, type_chat: string = 'free'): Observable<any> {
    let room = idRoom;
    if (room === null) room = 0;
    return this.http.get(`${this.endpoint}/chat/${type_chat}/${room}`, httpOptions)
  }
}
