import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Pack } from 'src/app/interfaces/pack-credit.interface';
import { environment } from 'src/environments/environment';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class CreditService {
  endpoint = environment.endpoint;

  public listPack = ['De Bienvenue', 'Bronze', 'Argent', 'Or'];

  public packBienvenue: Pack[] = [
    {credit: 30, cout: 0.18, montant: 5.25, affaire: false}
  ];

  public packBronze: Pack[] = [
    {credit: 40, cout: 0.26, montant: 10.5, affaire: false},
    {credit: 82, cout: 0.26, montant: 21, affaire: false},
    {credit: 123, cout: 0.26, montant: 31.5, affaire: false},
    {credit: 164, cout: 0.23, montant: 42, affaire: false},
    {credit: 225, cout: 0.23, montant: 52.5, affaire: true},
    {credit: 360, cout: 0.26, montant: 84 , affaire: false}
  ];

  public packArgent: Pack[] = [
    {credit: 470, cout: 0.21, montant: 210, affaire: false},
    {credit: 564, cout: 0.21, montant: 315, affaire: false},
    {credit: 720, cout: 0.21, montant: 420, affaire: false},
    {credit: 864, cout: 0.21, montant: 525, affaire: false},
  ];

  public packOr: Pack[] = [
    {credit: 1000, cout: 0.21, montant: 210, affaire: false},
    {credit: 1500, cout: 0.21, montant: 315, affaire: false},
    {credit: 2000, cout: 0.21, montant: 420, affaire: false},
    {credit: 2500, cout: 0.21, montant: 525, affaire: false},
  ];

  constructor(
    private http: HttpClient
  ) { }

  getCredit(): Observable<any> {
    return this.http.get(`${this.endpoint}/credit`, httpOptions)
  }

  createCredit(id: number): Observable<any> {
    return this.http.post(`${this.endpoint}/credit/create/${id}`,{id}, httpOptions)
  }

  createCreditClient(id: number): Observable<any> {
    return this.http.post(`${this.endpoint}/credit/create/client/${id}`,{id}, httpOptions)
  }

  updateCredit(id: number, credit: number) {
    const data = {
      credit
    }
    return this.http.patch(`${this.endpoint}/credit/${id}`, data, httpOptions)
  }

  buyCreditClient(montant: number, credit: number, creditId: number, clientId: number) {
    const data = {
      montant,
      credit,
      creditId,
      clientId
    }
    return this.http.patch(`${this.endpoint}/credit/client`, data, httpOptions)
  }

}
