import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { GetModel } from '../../interfaces/get-model.interface';

interface model {
  url: string,
  name: string
}

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class ModelService {
  endpoint = environment.endpoint


  live: model [] = [
    {url: 'assets/images/image3.jpg', name: 'Roddy'},
    {url: 'assets/images/image2.jpg', name: 'Monica'},
    {url: 'assets/images/image.png', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Monica'},
    {url: 'assets/images/image.png', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Angelina'}
  ];

  chat: model [] = [
    {url: 'assets/images/image2.jpg', name: 'Monica'},
    {url: 'assets/images/image2.jpg', name: 'Monica'},
    {url: 'assets/images/image2.jpg', name: 'Angelina'},
    {url: 'assets/images/image2.jpg', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Angelina'},
    {url: 'assets/images/image2.jpg', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Angelina'},
    {url: 'assets/images/image.png', name: 'Angelina'}
  ];

  offline: model [] = [
    {url: 'assets/images/image3.jpg', name: 'Angelina'},
    {url: 'assets/images/image3.jpg', name: 'Monica'},
    {url: 'assets/images/image.png', name: 'Angelina'}
  ];

  constructor(
    private http: HttpClient
  ) { }

  getTotalLive() {
    return this.live.length;
  }

  getTotalChat() {
    return this.chat.length;
  }

  getTotalOffline() {
    return this.offline.length;
  }

  getModelProfil(context: string, debut: number, marge: number) {
    var tmp = [];
    switch (context) {
      case 'live':
      tmp = this.live.slice(debut, debut + marge);
      break;

      case 'chat':
        tmp = this.chat.slice(debut, debut + marge);
        break;

      case 'offline':
        tmp = this.offline.slice(debut, debut + marge);
        break;

      default:
        break;
    }
    return tmp;
  }

  getAll(context: string) {
    var tmp = [];
    switch (context) {
      case 'live':
        tmp =  [...this.live];
        break;

      case 'chat':
        tmp =  [...this.chat];
        break;

      case 'offline':
        tmp =  [...this.offline];
        break;

      default:
        break;
    }
    return tmp;
  }

  // return live, chat, offline
  count(): Observable<any> {
    return this.http.get(`${this.endpoint}/model/total`, httpOptions);
  }

  // return array Model
  getList(data: GetModel): Observable<any> {
    return this.http.post(`${this.endpoint}/model/list`, data, httpOptions)
  }

  getModel(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/model/${id}`, httpOptions);
  }

  getInfo(): Observable<any> {
    return this.http.get(`${this.endpoint}/model/info`, httpOptions);
  }

  getLive(): Observable<any> {
    return this.http.get(`${this.endpoint}/model/live`, httpOptions);
  }

  getNoLive(): Observable<any> {
    return this.http.get(`${this.endpoint}/model/nolive`, httpOptions);
  }

  forgot(email : string) : Observable<any>{
    const options = {
      email : email
    }
    return this.http.post(`${this.endpoint}/model/forgot`, options, httpOptions)
  }

  reinitPassword(userId: number, password: string) : Observable<any>{
    const options = {
      id : userId,
      newPassword: password
    }
    return this.http.post(`${this.endpoint}/model/reinitPassword`, options, httpOptions)
  }

}
