import { Injectable } from '@angular/core';
import { LocalStorageService } from 'ngx-localstorage';
import { nextTick } from 'process';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class HeaderService {
  hidded = new Subject<boolean>();
  unselected = new Subject<boolean>();
  active = new Subject<number>();
  connect  = new Subject<boolean>();

  constructor() { }

  hideHeader(data: boolean) {
    this.hidded.next(data);
  }

  unselect(data: boolean) {
    this.unselected.next(data);
  }

  activate(data: number) {
    this.active.next(data);
  }

  connexion(data: boolean) {
    this.connect.next(data);
  }
}
