import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ProfilService } from '../../../services/profil/profil.service';
import { LocalStorageService } from 'ngx-localstorage';
import { ClientService } from '../../../services/client/client.service';
import { ModelService } from '../../../services/model/model.service';
import { AuthService } from '../../../services/auth/auth.service';
import { LogService } from '../../../services/log/log.service';
import { RoomService } from '../../../services/room/room.service';

@Component({
  selector: 'app-new-password',
  templateUrl: './new-password.component.html',
  styleUrls: ['./new-password.component.scss']
})
export class NewPasswordComponent implements OnInit {
  token  = null;
  newPassword: string = null;
  confirmPassword: string = null;
  errorToken: string = null;
  loading: boolean = false;

  info = {
    id: 0,
    role: null
  }

  constructor(
    private activatedRoute: ActivatedRoute,
    private profilService: ProfilService,
    private store: LocalStorageService,
    private clientService: ClientService,
    private modelService: ModelService,
    private router: Router,
    private authService: AuthService,
    private logService: LogService,
    private roomService: RoomService
  )
  {
    this.activatedRoute.queryParams.subscribe(params => {
      this.token = params['token'];
    });
  }

  ngOnInit(): void {
    this.verifyToken();
  }

  verifyToken() {
    if (!this.token) {
      this.errorToken = "Token invalid";
      return null;
    }
    this.store.set("token", this.token);
    this.profilService.getCredential().subscribe(
      (data: {id: number, role: string}) => {
        if (data.id && data.role) {
          this.info.id = data.id;
          this.info.role = data.role;
        } else {
          this.errorToken = "Token invalid";
        }
        console.log("Credential ", data);
      }, (error) => {
        this.errorToken = "Token invalid";
      }
    )
  }

  send() {
    this.errorToken = null;

    if (this.newPassword !== this.confirmPassword) {
      this.errorToken = "Mot de passe ne correspond pas";
      return null;
    }

    if (!this.newPassword || !this.confirmPassword) {
      this.errorToken = "Veuillez completer les champs";
      return null;
    }

    this.loading = true;

    if (this.info.role === 'model') {
      this.modelService.reinitPassword(this.info.id, this.newPassword).subscribe(
        (data) => {
          // console.log(" reinit model ",data);
          if (data.access_token) {
            this.authService.saveToken(data.access_token).then(
              () => {
                this.saveLog();
                this.roomService.createRoom().subscribe(
                    (data) => {
                      this.loading = false;
                      this.store.set("room", data.room);
                      this.router.navigate(['/modele/profile']);
                    }
                )
              }, (error) => {
                console.log(error);
                this.loading = false;
              }
            );
          }
        }
      )
    } else if (this.info.role === 'client') {
      this.clientService.reinitPassword(this.info.id, this.newPassword).subscribe(
        (data) => {
          // console.log(" reinit client ",data);
          if (data.access_token) {
            this.authService.saveToken(data.access_token, data.id).then(
              () => {
                    this.loading = false;
                    this.router.navigate(['/accueil/registered']);
              }
            );
          }
        }, (error) => {
          console.log(error);
          this.loading = false;
        }
      )
    }
  }

  saveLog() {
    this.logService.createLog("reinitialisation mot de passe").subscribe(
      (data) => {
        // console.log('Log ', data)
      }, (error) => {
        console.log(error);
      }
    );
  }
}
