import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { Observable, Subject } from 'rxjs';
import { environment } from 'src/environments/environment';
import { loginUser } from 'src/app/interfaces/login.interface';
import { registerClient, registerModel } from '../../interfaces/register.interface';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  endpoint = environment.endpoint

  authentified  = new Subject<boolean>();

  constructor(
    private store: LocalStorageService,
    private http : HttpClient,
    public router : Router
  ) { }

  login(who: string) {
    if (who == 'model') {
      console.log("connexion model");
      this.store.set("model", true);
    }
    this.authentication(true);
  }

  logout(who: string) {
    if (who == 'model') {
      console.log("deconnexion model");
      this.store.set("model", false);
    }
    this.authentication(false);
  }

  authentication(data: boolean) {
    this.authentified.next(data);
    this.store.set("auth", data);
  }

  loginClient(user : loginUser) : Observable<any>{
    return this.http.post(`${this.endpoint}/client/login`,user, httpOptions)
  }

  loginModel(user : loginUser) : Observable<any>{
    return this.http.post(`${this.endpoint}/model/login`,user, httpOptions)
  }

  registerClient(user : registerClient) : Observable<any>{
    return this.http.post(`${this.endpoint}/client/register`,user,httpOptions)
  }

  registerModel(user : registerModel) : Observable<any>{
    return this.http.post(`${this.endpoint}/model/register`,user,httpOptions)
  }

  uploadImagesModel(formData: FormData): Observable<any> {
    return this.http.post(`${this.endpoint}`,formData,{
        reportProgress: true,
        observe: 'events'
    });
  }

  // Not
  // confirm(email : string , password : string, codeConfirmUser : string) : Observable<any>{
  //   return this.http.put(`${this.endpoint}/confirmation`,{email,password, codeConfirmUser}, httpOptions)
  // }

  // Not
  // resendConfirm(email : string) : Observable<any>{
  //   return this.http.post(`${this.endpoint}/resend`,{email}, httpOptions)
  // }

  // Not
  forgot(email : string) : Observable<any>{
    const options = {
      email : email
    }
    return this.http.post(`${this.endpoint}/forgot`, options, httpOptions)
  }

  checkTokenValidation(token : string) : Observable<any>{
    return this.http.get(`${this.endpoint}/forgot?token=${token}`)
  }

  changePassowrd(password : string,newPassword : string) : Observable<any>{
    return this.http.put(`${this.endpoint}/password`,{password : password, newPassword : newPassword},httpOptions)
  }

  updatePassword(token : string,password : string) : Observable<any>{
    return this.http.put(`${this.endpoint}/forgot`,{token,password},httpOptions)
  }

  getToken(){
    let token = this.store.get("token");
    if (token === null) {
      return false;
    }
    return token;
  }

  get isConnected() : boolean{
    let authToken = this.store.get("token");
    return authToken != null
  }

  logoutUser(who: string, route: string){
    let removeToken =  this.store.set("token", null);
        if (removeToken == null) {
            // this.router.navigate(['connexion']);
            this.router.navigate([route]);
        }
  }

  async saveToken(token, id?: number) {
    if (id) await this.store.set("identification//", id);
    await this.store.set("token", token);
  }

}
