import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { LocalStorageService } from 'ngx-localstorage';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};


@Injectable({
  providedIn: 'root'
})
export class RoomService {
  endpoint = environment.endpoint

  constructor(
    private http: HttpClient,
    private store: LocalStorageService
  ) { }

  getColor(): Observable<any> {
    return this.http.get(`${this.endpoint}/room/color`, httpOptions)
  }

  createRoom(): Observable<any> {
    return this.http.post(`${this.endpoint}/room`,{}, httpOptions)
  }

  getRoomModel(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room/model/${id}`, httpOptions)
  }

  getInfo(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room/client/${id}`, httpOptions)
  }

  async getActif(id: number): Promise<number> {
    let actif = 0;
    await this.http.get(`${this.endpoint}/room/${id}`, httpOptions).subscribe(
      (data: any) => {
        if (data.actif) {
          actif =  data.actif
        }
      }
    );
    return actif;
  }

  joinRoom() {
    this.store.set("freeRoom", true);
  }

  joinedRoom() {
    return this.store.get("freeRoom") ? true : false;
  }

  leaveRoom() {
    this.store.remove("freeRoom");
  }

}
