import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AlbumService } from '../../services/album/album.service';
import { NotificationService } from '../../services/notification/notification.service';

@Component({
  selector: 'app-popup-image',
  templateUrl: './popup-image.component.html',
  styleUrls: ['./popup-image.component.scss']
})
export class PopupImageComponent implements OnInit {

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: {id: number, url: string, type: string},
    private albumService: AlbumService,
    private notificationService: NotificationService,
    public dialogRef: MatDialogRef<PopupImageComponent>
    ) { }

  ngOnInit(): void {
  }

  close() {
    this.dialogRef.close('close');
  }

  delete(id: number) {
    this.albumService.deleteAlbum(id).subscribe(
      (data) => {
        this.notificationService.info("Suppression", "Photo Supprime");
        this.dialogRef.close('success');
      }
    )
  }

}
