import { Component, OnInit } from '@angular/core';
import { CreditService } from '../../../../services/credit/credit.service';

@Component({
  selector: 'app-solde',
  templateUrl: './solde.component.html',
  styleUrls: ['./solde.component.scss']
})
export class SoldeComponent implements OnInit {
  coinImg = "assets/icons/coin-stack.svg";
  solde = 500;

  constructor(
    private creditService: CreditService
  ) { }

  ngOnInit(): void {
    this.getCredit();
  }

  getCredit() {
    this.creditService.getCredit().subscribe(
      (data) => {
        console.log(data);
        this.solde = data.credit;
      }
    )
  }

}
