import { AuthService } from 'src/app/services/auth/auth.service';
import { HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, throwError } from "rxjs";

import { catchError, retry } from "rxjs/operators";
import { Router } from "@angular/router";
import { NotificationService } from '../notification/notification.service';
@Injectable()
export class AuthInterceptor implements HttpInterceptor{

    constructor(
      private authService : AuthService,
      public router : Router,
      private notificationService: NotificationService){
    }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        const token = this.authService.getToken()
        let header = req.headers

        if ( this.authService.isConnected )
        {
            req = req.clone({
                setHeaders : {
                    Authorization : 'Bearer '+ token
                }
            });
        }

        return next.handle(req)
        .pipe(
            retry(1),
            catchError((error : HttpErrorResponse)=>{
                // let errorMessage = '';

                    if(error.error.statusCode == 400){
                      this.notificationService.errorMsg(error.error.message);
                    } else if(error.error.statusCode == 401){
                      this.notificationService.errorMsg('Accs refus');
                    } else {
                      this.notificationService.errorMsg("Erreur lors de la connexion au serveur");
                    }
                //     if(error.status == 500){
                //         // this.router.navigateByUrl("connexion")
                //         // this.dialogService.erreur('500');
                //     }
                //     if (error.error instanceof ErrorEvent) {
                //         // client-side error
                //         errorMessage = `Error: ${error.error.message}`;
                //     }
                    console.log('Erreur interceptor ', error.error);
                    return throwError(error);
            })
        )

    }

}
