import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { NotificationService } from 'src/app/services/notification/notification.service';
import { environment } from 'src/environments/environment';
import { ClientService } from '../../../services/client/client.service';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Component({
  selector: 'app-reactivate-account',
  templateUrl: './reactivate-account.component.html',
  styleUrls: ['./reactivate-account.component.scss']
})
export class ReactivateAccountComponent implements OnInit {
  endpoint = environment.endpoint

  token = null;
  errorToken: boolean = false;
  errorMessageToken: string = null;
  successMessageToken: string = null;


  constructor(
    private activatedRoute: ActivatedRoute,
    private http: HttpClient,
    private store: LocalStorageService,
    private router: Router,
    private notificationService: NotificationService,
    private clientService: ClientService
    ) {
    this.activatedRoute.queryParams.subscribe(params => {
          this.token = params['token'];
      });
  }

  ngOnInit(): void {
    this.confirmToken();
  }

  confirmToken() {
    if (!this.token) {
      this.errorToken = true;
      this.errorMessageToken = "Token introuvable"
      return false;
    }
    this.store.set("token", this.token);

    // POST client/confirm
    this.clientService.reactivateAccount().subscribe(
      (data: {success: boolean, message: string}) => {
        // console.log("Client token ", data);
        if (!data.success) {
          this.errorMessageToken = data.message;
          this.errorToken = true;
        } else {
          this.errorToken = false;
          this.successMessageToken = data.message;
          this.notificationService.infoMsg("Votre compte est ractiv");
          this.router.navigate(['/connexion']);
        }
      }
    )
  }

}
