import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { LocalStorageService } from 'ngx-localstorage';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class RoomVipService {
  endpoint = environment.endpoint

  constructor(
    private http: HttpClient,
    private store: LocalStorageService
  ) { }

  getColor(): Observable<any> {
    return this.http.get(`${this.endpoint}/room-vip/color`, httpOptions)
  }

  createRoom(clientId: number): Observable<any> {
    return this.http.post(`${this.endpoint}/room-vip`,{clientId}, httpOptions)
  }

  getRoomModel(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-vip/model/${id}`, httpOptions)
  }

  getInfo(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-vip/client/${id}`, httpOptions)
  }

  async getActif(id: number): Promise<number> {
    let actif = 0;
    await this.http.get(`${this.endpoint}/room-vip/${id}`, httpOptions).subscribe(
      (data: any) => {
        if (data.actif) {
          actif =  data.actif
        }
      }
    );
    return actif;
  }

  joinRoom(idRoom: number) {
    this.store.set("vipRoom", idRoom);
  }

  joinedRoom(idRoom: number) {
    const room = this.store.get("vipRoom");
    return room === idRoom;
  }

  leaveRoom() {
    this.store.remove("vipRoom");
  }

  getGain(modelId: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-vip/gain/${modelId}`, httpOptions)
  }

}
