import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Router } from '@angular/router';

@Component({
  selector: 'app-popup-info',
  templateUrl: './popup-info.component.html',
  styleUrls: ['./popup-info.component.scss']
})
export class PopupInfoComponent implements OnInit {
  title = '';
  message = '';
  action = '';
  constructor(
    public dialogRef: MatDialogRef<PopupInfoComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private router: Router
  ) {
    this.action = data.action;
    this.title = data.title;
    this.message = data.message;
   }

  ngOnInit(): void {
  }

  async validate(): Promise<void> {
    if (this.action == 'client') {
      this.router.navigate(['/connexion'])
    } else if (this.action == 'model') {
      this.router.navigate(['/modele/connexion'])
    } else if (this.action ===  null) {
      // nothing
    } else {
      this.router.navigate([this.action])
    }
    return this.dialogRef.close();
  }

}
