import { AuthService } from './../../../services/auth/auth.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import * as $  from 'jquery';
import { Router } from '@angular/router';
import { registerModel } from '../../../interfaces/register.interface';
import { PopupService } from '../../../services/popup/popup.service';
import { HttpClient, HttpEventType } from '@angular/common/http';
import { ProfilService } from '../../../services/profil/profil.service';
import { CreditService } from '../../../services/credit/credit.service';

interface birth {
  value: string,
  name: string
}

@Component({
  selector: 'app-inscription-model',
  templateUrl: './inscription-model.component.html',
  styleUrls: ['./inscription-model.component.scss']
})
export class InscriptionModelComponent implements OnInit {
  signForm : FormGroup;
  submitted :boolean =  false;
  loading: boolean = false;

  error: string = null;
  emailExist: boolean = false;
  pseudoExist: boolean = false;

  day: birth[] = [
    {value: '01', name: '01'},{value: '02', name: '02'},{value: '03', name: '03'},{value: '04', name: '04'},
    {value: '05', name: '05'},{value: '06', name: '06'},{value: '07', name: '07'},{value: '08', name: '08'},
    {value: '09', name: '09'}
  ];

  month: birth[] = [
    {value: '01', name: 'Janvier'}, {value: '02', name: 'Fvrier'}, {value: '03', name: 'Mars'}, {value: '04', name: 'Avril'},
    {value: '05', name: 'Mai'}, {value: '06', name: 'Juin'}, {value: '07', name: 'Juillet'}, {value: '08', name: 'Aot'},
    {value: '09', name: 'Septembre'}, {value: '10', name: 'Octobre'}, {value: '11', name: 'Novembre'}, {value: '12', name: 'Dcembre'}
  ];

  year: birth[] = [];

  fileRecto: File = null;
  previewRecto: any;
  nameRecto: any;
  errorRecto: boolean = false;

  fileVerso: File = null;
  previewVerso: any;
  nameVerso: any;
  errorVerso: boolean = false;


  filePhoto: File = null;
  previewPhoto: any;
  namePhoto: any;
  errorPhoto: boolean = false;


  fileSoft: File = null;
  previewSoft: any;
  nameSoft: any;
  errorSoft: boolean = false;


  fileUploadProgress: string = null;

  constructor(
    private formBuilder : FormBuilder,
    private popupService: PopupService,
    private authService: AuthService,
    private profileService: ProfilService,
    private creditService: CreditService
  ) {
    this.signForm = this.formBuilder.group({
      firstname : ['', Validators.required],
      lastname : ['', Validators.required],
      pseudo : ['', Validators.required],
      email : ['',[Validators.required, Validators.email]],
      // password : ['',[Validators.required, Validators.pattern(/^(?=\D*\d)(?=[^a-z]*[a-z])(?=[^A-Z]*[A-Z]).{8,}$/)]],
      day_birth : ['', Validators.required],
      month_birth : ['', Validators.required],
      year_birth : ['', Validators.required],
      path_recto : [''],
      path_verso : [''],
      path_cin : [''],
      path_soft : [''],
      password : ['', Validators.required],
      passwordConfirm: ['',Validators.required],
    },{
      validators : this.mustMatch('password','passwordConfirm'),
    })
  }

  ngOnInit(): void {
    this.initDay();
    this.initYear();
  }

  get form() {return this.signForm.controls}

  mustMatch = (controlName : string, matchControlName : string) => {
    return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];
      const matchingControl = formGroup.controls[matchControlName];

      if (matchingControl.errors && !matchingControl.errors.mustMatch) {
          return;
      }
      if (control.value !== matchingControl.value) {
          matchingControl.setErrors({ mustMatch: true });
      } else {
          matchingControl.setErrors(null);
      }
    }
  }

  initDay() {
    for (var i = 10; i < 32; i++) {
      const tmp: birth = {value: i+'', name: i+''};
      this.day.push(tmp);
    }
  }

  initYear() {
    for (var i = 1920; i < 2500; i++) {
      const tmp: birth = {value: i+'', name: i+''};
      this.year.push(tmp);
    }
  }

  selectPhoto(type: string) {
    $(`#${type}`).click();
  }

  verifyMimeType(mime: string) {
    return (mime === "image/png" || mime === "image/jpeg");
  }

  fileChoose(fileInput, type: string) {
    var ok: boolean = false;
    switch (type) {
      case 'recto':
        ok = true;
        this.fileRecto =  fileInput.target.files[0];
        break;
      case 'verso':
        ok = true;
        this.fileVerso =  fileInput.target.files[0];
        break;
      case 'photo':
        ok = true;
        this.filePhoto =  fileInput.target.files[0];
        break;
      case 'soft':
        ok = true;
        this.fileSoft =  fileInput.target.files[0];
        break;
      default:
        break;
    }
    if (ok) {this.preview(type);}
  }

  preview(type: string) {
    this.errorRecto = this.errorVerso = this.errorPhoto = this.errorSoft = false;
    var mimeType: any;
    var reader = new FileReader();
    var ok: boolean = false;

    switch (type) {
      case 'recto':
        ok = true;
        mimeType = this.fileRecto.type;
        if (!this.verifyMimeType(mimeType)) this.errorRecto = true;
        reader = new FileReader();
        reader.readAsDataURL(this.fileRecto);
        reader.onload = (event) => {
          this.previewRecto = reader.result;
          this.nameRecto = this.fileRecto.name;
        };
        break;

      case 'verso':
        ok = true;
        mimeType = this.fileVerso.type;
        if (!this.verifyMimeType(mimeType)) this.errorVerso = true;
        reader = new FileReader();
        reader.readAsDataURL(this.fileVerso);
        reader.onload = (event) => {
          this.previewVerso = reader.result;
          this.nameVerso = this.fileVerso.name;
        };
        break;

      case 'photo':
        ok = true;
        mimeType = this.filePhoto.type;
        if (!this.verifyMimeType(mimeType)) this.errorPhoto = true;
        reader = new FileReader();
        reader.readAsDataURL(this.filePhoto);
        reader.onload = (event) => {
          this.previewPhoto = reader.result;
          this.namePhoto = this.filePhoto.name;
        };
        break;

      case 'soft':
        ok = true;
        mimeType = this.fileSoft.type;
        if (!this.verifyMimeType(mimeType)) this.errorSoft = true;
        reader = new FileReader();
        reader.readAsDataURL(this.fileSoft);
        reader.onload = (event) => {
          this.previewSoft = reader.result;
          this.nameSoft = this.fileSoft.name;
        };
        break;

      default:
        break;
    }
  }

  select() {
    $(".selected_option").hide();
  }

  async register() {
    this.pseudoExist = false;
    this.emailExist = false;
    this.error = null;
    this.submitted = true;

    if(this.signForm.invalid || this.errorSoft || this.errorRecto || this.errorVerso || this.errorPhoto){
      return false;
    }
    this.loading = true;

    const formData = new FormData();
    formData.append('file_recto', this.fileRecto, this.nameRecto);
    formData.append('file_verso', this.fileVerso, this.nameVerso);
    formData.append('file_cin', this.filePhoto, this.namePhoto);
    formData.append('file_soft', this.fileSoft, this.nameSoft);
    this.fileUploadProgress = '0%';

    return await this.authService.uploadImagesModel(formData).subscribe(
      (events) => {
        if (events.type === HttpEventType.UploadProgress) {
          this.fileUploadProgress = Math.round(events.loaded / events.total * 100) + '%';
          // console.log(this.fileUploadProgress);
        }
        else if (events.type === HttpEventType.Response) {
          let user: registerModel = this.signForm.value;
          user = this.signForm.value;
          user.date_birth = user.day_birth + '/' + user.month_birth + '/' + user.year_birth;
          user.path_recto = events.body.path_recto;
          user.path_verso = events.body.path_verso;
          user.path_cin = events.body.path_cin;
          user.path_soft = events.body.path_soft;
          this.authService.registerModel(user).subscribe(
            (res: any) => {
                // console.log(res);
                if (res.error) {
                  this.error = res.message;
                  if (res.pseudo) this.pseudoExist = true;
                  if (res.email) this.emailExist = true;
                } else {
                  this.profileService.createSetting(res.id).subscribe(
                    (data) => {
                      this.creditService.createCredit(res.id).subscribe(
                        (data) => {
                          this.profileService.createProfil(res.id).subscribe(
                            (data) => {
                              this.popupService.info('model','Flicitation',
                              `Cher nouveau modle, Un email est envoy 
                              <span style="font-style: italic; color: var(--pink)">${res.email}</span>
                              pour les informations de votre compte.`);
                              this.signForm.reset();
                            }
                          );
                        }
                      );
                    }
                  );

                }
                this.loading = false;
            },
            (err: any) => {
                this.loading = false;
                console.log(err);
            }
          );
        }
      }, (error) => {
        console.log(error);
      }
    )
  }

}
