import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { ClientService } from '../../../../services/client/client.service';
import { NotificationService } from '../../../../services/notification/notification.service';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-password',
  templateUrl: './password.component.html',
  styleUrls: ['./password.component.scss']
})
export class PasswordComponent implements OnInit {
  passwordForm: FormGroup;
  submitted: boolean = false;
  errorPassword = null;

  constructor(
    private formBuilder: FormBuilder,
    private clientService: ClientService,
    private notificationService: NotificationService,
    public dialogRef: MatDialogRef<PasswordComponent>
    ) {
    this.passwordForm = this.formBuilder.group({
      oldPassword : ['', Validators.required],
      // newPassword : ['',[Validators.required, Validators.pattern(/^(?=\D*\d)(?=[^a-z]*[a-z])(?=[^A-Z]*[A-Z]).{8,}$/)]],
      newPassword : ['', Validators.required],
      confirmPassword: ['',Validators.required],
    },{
      validators : this.mustMatch('newPassword','confirmPassword'),
    })
  }

  ngOnInit(): void {
  }

  get form() {return this.passwordForm.controls}

  mustMatch = (controlName : string, matchControlName : string) => {
    return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];
      const matchingControl = formGroup.controls[matchControlName];

      if (matchingControl.errors && !matchingControl.errors.mustMatch) {
          return;
      }
      if (control.value !== matchingControl.value) {
          matchingControl.setErrors({ mustMatch: true });
      } else {
          matchingControl.setErrors(null);
      }
  }
  }

  async validate(event): Promise<void> {
    this.submitted = true;
    event.preventDefault();
    this.errorPassword = null;
    if(this.passwordForm.invalid){
      return;
    }
    let passwords = this.passwordForm.value;
    this.clientService.changePassword(passwords.oldPassword, passwords.newPassword).subscribe(
      (data: any) => {
        console.log("change Password ", data);
        if (data.error) {
          this.errorPassword = data.message;
        } else {
          this.notificationService.success("Mot de passe", "Modifi");
          this.dialogRef.close("success");
        }
      }
    )
  }

}
