import { EncryptionService } from './../../../services/encryption/encryption.service';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HeaderService } from '../../../services/header/header.service';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth/auth.service';
import { environment } from 'src/environments/environment';
import { loginUser } from '../../../interfaces/login.interface';
import * as $ from 'jquery';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent implements OnInit {
  loginForm : FormGroup;
  submitted :boolean =  false;
  loading: boolean = false;
  checked :boolean =  false;
  pseudoStorage : string = null;
  passStorage : string = null;
  errorAuth  :boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private encryptionService: EncryptionService,
    private headerSrv: HeaderService,
    private authService: AuthService,
    private router: Router,
  ) {
    this.loginForm = this.formBuilder.group({
      pseudo: ['', Validators.required],
      password: ['', Validators.required]
    })
  }

  ngOnInit(): void {
    this.isRemembered();
  }

  get form() { return this.loginForm.controls }

  remember(value) {
    if (value.target.checked) {
        this.checked = true;
        localStorage.setItem("checklog", "true");
    } else {
        this.checked = false;
        localStorage.setItem("checklog", "false");
    }
  }

  async isRemember(user: loginUser) {
    if (this.checked) {
        const pseudoCrypted = this.encryptionService.encryptUsingAES256(user.pseudo);
        const passCrypted = this.encryptionService.encryptUsingAES256(user.password);
        localStorage.setItem(environment.CRYPT_PSEUDO, pseudoCrypted);
        localStorage.setItem(environment.CRYPT_PASS, passCrypted);
    } else {
        localStorage.setItem(environment.CRYPT_PSEUDO, null);
        localStorage.setItem(environment.CRYPT_PASS, null);
    }
  }

  isRemembered() {
    const pseudoStorage = localStorage.getItem(environment.CRYPT_PSEUDO) ? localStorage.getItem(environment.CRYPT_PSEUDO) : null;
    const passStorage = localStorage.getItem(environment.CRYPT_PASS) ? localStorage.getItem(environment.CRYPT_PASS) : null;

    if (pseudoStorage && passStorage) {
      this.pseudoStorage = this.encryptionService.decryptUsingAES256(pseudoStorage);
      this.passStorage = this.encryptionService.decryptUsingAES256(passStorage);

      this.pseudoStorage = this.pseudoStorage.replace(/['"]+/g, '');
      this.passStorage = this.passStorage.replace(/['"]+/g, '');

      this.loginForm.setValue({
            pseudo: this.pseudoStorage,
            password: this.passStorage
      })
    }

    if (localStorage.getItem("checklog") === "true")
      $('#checker').prop('checked', true);
    else
      $('#checker').prop('checked', false);
  }


  async login(event) {
    this.submitted = true;
    event.preventDefault();
    if (this.loginForm.invalid) {return false;}
    this.loading = true;
    let user: loginUser = this.loginForm.value;
    await this.authService.loginClient(user).subscribe(
      (res: any) => {
          this.loading = false;
          console.log(res);
          if (res.error) {
            this.errorAuth = res.message;
          } else {
            this.errorAuth = null;
            if (res.access_token)
              this.authService.saveToken(res.access_token, res.id).then(
                () => {
                  this.isRemember(user).then(
                    () => {
                      this.router.navigate(['/accueil/registered']);
                    }
                  );
                }
              );
          }
      },
      (err: any) => {
          this.loading = false;
          console.log(err);
      }
    );

    return true;
}

}
