import { AuthService } from 'src/app/services/auth/auth.service';
import { HeaderService } from './../../../services/header/header.service';
import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { AlbumService } from '../../../services/album/album.service';
import { FileUploader } from 'ng2-file-upload';
import { ActivatedRoute } from '@angular/router';
import { NotificationService } from '../../../services/notification/notification.service';
import { HttpEventType } from '@angular/common/http';
import { addAlbum } from '../../../interfaces/add-album.interface';
import { ProfilService } from '../../../services/profil/profil.service';
import { PopupImageComponent } from '../../../modals/popup-image/popup-image.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-albums',
  templateUrl: './albums.component.html',
  styleUrls: ['./albums.component.scss']
})
export class AlbumsComponent implements OnInit {
  bg = "assets/images/bg.jpg";
  profil = "assets/images/image.png";
  photoAdd = "assets/icons/photoAdd.svg";
  bannier = "assets/logos/dark-light.jpg";


  totalAlbum: number  = 0;
  sectionAlbum: number = 9;
  page: number = 0;

  contextAlbum = "private";
  currentAlbum: addAlbum[] = [];
  textAlbum: any;
  fileData: File = null;
  previewData: any;
  nameData: any;

  albums: File[] = [];
  file: File;
  fileUploadProgress: string;

  count = {
    "vip": 0,
    "private": 0,
    "free": 0
  }

  info = {
    pseudo: '',
    email: '',
    path_soft: null
  }

  formData = new FormData();

  constructor(
    private profileService: ProfilService,
    private route: ActivatedRoute ,
    private notificationService: NotificationService,
    private albumService: AlbumService,
    private dialog: MatDialog){
    let param = this.route.snapshot.queryParamMap.get('context');
    if (param != null) {this.contextAlbum = param;}
  }

  ngOnInit(): void {
    this.initAlbum(this.contextAlbum);
    this.countAlbum();
    this.getInfo();
  }

  getInfo() {
    this.profileService.getInfo().subscribe(
      (data: any) => {
        // console.log('album ', data);
        this.info = data;
      }
    )
  }

  async countAlbum() {
    this.albumService.getCount().subscribe(
      (data) => {
        this.count = data;
        if (this.contextAlbum == 'private')
          this.totalAlbum = this.count.private; // Default onglet
        else if (this.contextAlbum == 'vip')
          this.totalAlbum = this.count.vip; // Default onglet
        else if (this.contextAlbum == 'free')
          this.totalAlbum = this.count.free; // Default onglet
      }
    )
  }

  async fetchAlbum(debut: number) {
    this.currentAlbum = [];
    await (await this.albumService.getAlbums(this.contextAlbum, debut, 9)).subscribe(
      (data) => {
        this.currentAlbum = data;
        console.log(this.currentAlbum);
      }
    )
  }

  // context : private |vip | free
  initAlbum(context: string) {
    this.contextAlbum = context;
    var verified  = false;
    switch (context) {
      case 'private':
        verified = true;
        this.totalAlbum = this.count.private;
        this.textAlbum = "Contenu Privs";
        break;
      case 'vip':
        verified = true;
        this.totalAlbum = this.count.vip;
        this.textAlbum = "Album VIP";
        break;
      case 'free':
        verified = true;
        this.totalAlbum = this.count.free;
        this.textAlbum = "Album Free";
        break;
      default:
        break;
    }
    if (verified) {
      this.fetchAlbum(0);
    }
  }


  // action: prev | next
  take(action: string) {
    if (action === "next") {
      this.page =  this.page + 1;
      var marge = Math.floor((this.totalAlbum/this.sectionAlbum) - 0.1); // - 0.1 avoid just division result
      if (this.page <= marge) {
        this.fetchAlbum(this.page * this.sectionAlbum);
        this.scroll();
      } else { this.page = this.page -1;}
    } else if (action === "prev" && this.page > 0) {
      this.page = this.page - 1;
      this.fetchAlbum(this.page * this.sectionAlbum);
      this.scroll();
    }
  }

  verifyMimeType(mime: string) {
    return (mime === "image/png" || mime === "image/jpeg" || mime === "image/gif");
  }

  scroll() {
    document.querySelector('#scrolled_point').scrollIntoView();
  }

  async fileChoose(fileInput) {
    const formData = new FormData();
    let file: File;
    for (let index = 0; index < fileInput.target.files.length; index++) {
      file = fileInput.target.files[index];
      const mimeType = file.type;
      if (!this.verifyMimeType(mimeType)) {
        this.notificationService.failure('Format invalide', 'Extension au format JPEG/PNG');
        return false;
      }
      formData.append('files', file, file.name);
    }

    this.fileUploadProgress = '0%';
    return await this.albumService.uploadAlbumModel(formData).subscribe(
      (events) => {
        if (events.type === HttpEventType.UploadProgress) {
          this.fileUploadProgress = Math.round(events.loaded / events.total * 100) + '%';
        }
        else if (events.type === HttpEventType.Response) {
          const paths  = events.body.paths;
          const albums: addAlbum[] = [];
          for (let index = 0; index < paths.length; index++) {
            albums.push({
                "type_album": this.contextAlbum,
                "path_album": paths[index]
              })
          }
          this.albumService.addAlbums(albums).subscribe(
            (data) => {
              this.notificationService.success(`Photo ${this.contextAlbum}`, "Ajoute");
              this.countAlbum();
              this.fetchAlbum(this.page * this.sectionAlbum);
            }, (error) => {
              console.log(error);
            }
          )
        }
      }, (error) => {
        console.log(error);
      }
    )
  }

  async profilChoose(fileInput) {
    this.file =  fileInput.target.files[0];
    var mimeType: any;
    var reader = new FileReader();
    mimeType = this.file.type;
    if (!(mimeType === "image/png" || mimeType === "image/jpeg")) return false;
    reader = new FileReader();
    reader.readAsDataURL(this.file);
    reader.onload = (event) => {
      this.info.path_soft = reader.result;
      // this.nameRecto = this.fileRecto.name;
    };

    this.formData.delete('file');
    this.formData.append('file', this.file, this.file.name);
    this.profileService.updatePhoto(this.formData).subscribe(
      async (events) => {
        if (events.type === HttpEventType.UploadProgress) {
          // this.fileUploadProgress = Math.round(events.loaded / events.total * 100) + '%';
        }
        else if (events.type === HttpEventType.Response) {
          const path  = events.body.path;
          this.info.path_soft = path;
          await this.saveSetting();
        }
      }, (error) => {
        console.log(error);
      }
    )
  }

  async saveSetting() {
        this.profileService.updateInfo(this.info).subscribe(
          (data) => {
              this.notificationService.success("Modification", "Termin avec succs")
          }
        )
  }

  openImage(id: number, url: string) {
    let dialogRef = this.dialog.open(PopupImageComponent, {
      width: '100vh',
      data: {
        id,
        url,
        type: this.contextAlbum
      },
    });
    dialogRef.afterClosed().subscribe((data) => {
      if (data === 'success') this.ngOnInit();
    });
  }

}
