import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { LocalStorageService } from 'ngx-localstorage';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};


@Injectable({
  providedIn: 'root'
})
export class RoomPrivateService {
  endpoint = environment.endpoint

  constructor(
    private http: HttpClient,
    private store: LocalStorageService
  ) { }

  getColor(): Observable<any> {
    return this.http.get(`${this.endpoint}/room-private/color`, httpOptions)
  }

  createRoom(): Observable<any> {
    return this.http.post(`${this.endpoint}/room-private`,{}, httpOptions)
  }

  getRoomModel(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-private/model/${id}`, httpOptions)
  }

  getInfo(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-private/client/${id}`, httpOptions)
  }

  getActif(id: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-private/${id}`, httpOptions)
  }

  joinRoom(idRoom: number) {
    this.store.set("privateRoom", idRoom);
  }

  joinedRoom(idRoom: number) {
    const room = this.store.get("privateRoom");
    return room === idRoom;
  }

  leaveRoom() {
    this.store.remove("privateRoom");
  }

  updateActif(roomId: number, joined: boolean, type_room: string): Observable<any> {
    const data = {
      roomId,
      joined,
      type_room
    }
    // console.log("Update actif ", data);
    return this.http.post(`${this.endpoint}/actif-room-private`, data, httpOptions)
  }

  getGain(modelId: number): Observable<any> {
    return this.http.get(`${this.endpoint}/room-private/gain/${modelId}`, httpOptions)
  }

}
