import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import { ProfilService } from '../../../../services/profil/profil.service';
import { NotificationService } from '../../../../services/notification/notification.service';

@Component({
  selector: 'app-password-modele',
  templateUrl: './password-modele.component.html',
  styleUrls: ['./password-modele.component.scss']
})
export class PasswordModeleComponent implements OnInit {
  changeForm: FormGroup;
  submitted: boolean = false;

  old: string = '';
  new: string = '';
  confirm: string = '';

  constructor(
    private formBuilder: FormBuilder,
    private profileService: ProfilService,
    private notificationService: NotificationService
    ) {
    this.changeForm = this.formBuilder.group({
      oldPassword : ['', Validators.required],
      newPassword : ['', Validators.required],
      confirmPassword: ['',Validators.required],
    },{
      validators : this.mustMatch('newPassword','confirmPassword'),
    })
  }

  ngOnInit(): void {
  }

  get form() {return this.changeForm.controls}

  mustMatch = (controlName : string, matchControlName : string) => {
    return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];
      const matchingControl = formGroup.controls[matchControlName];

      if (matchingControl.errors && !matchingControl.errors.mustMatch) {
          return;
      }
      if (control.value !== matchingControl.value) {
          matchingControl.setErrors({ mustMatch: true });
      } else {
          matchingControl.setErrors(null);
      }
  }
  }

  async validate(event): Promise<void> {
    this.submitted = true;
    event.preventDefault();

    if(this.changeForm.invalid){
      return;
    }
    let passwords = this.changeForm.value;
    this.profileService.changePassword(passwords).subscribe(
      (data) => {
        this.submitted = false;
        console.log(data)
        if (data.error)
          this.notificationService.errorMsg("Mot de passe incorrect")
        else
          this.notificationService.success("Mot de passe", "Modification avec succs");
        this.changeForm.reset();
      }
    )
  }
}
