import { HeaderService } from './../../../services/header/header.service';
import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  pageId: number = 1;
  menu = "assets/icons/menu-dot.svg";
  close = "assets/icons/close.svg";
  slided = false;
  loaded: number = 1;
  constructor(
    private headerSrv: HeaderService
  ) {
    this.headerSrv.activate(3);
  }

  ngOnInit(): void {
  }

  load(position: number) {
    this.loaded = position;
  }

  link(id: number) {
    this.pageId = id;
    if (this.slided) {
      $("#left_side").addClass("slideHide");
      this.slided = false;
    }

  }

  slideMenu() {
    $("#left_side").removeClass("slideShow");
    $("#left_side").removeClass("slideHide");
    this.slided = !this.slided;
    if (this.slided) {
      $("#left_side").addClass("slideShow");
    } else {
      $("#left_side").addClass("slideHide");
    }
  }

}
