import { Component, OnInit,
  OnDestroy, PLATFORM_ID,
  Inject, ViewChild,
  ElementRef,
  HostListener} from '@angular/core';
import {isPlatformBrowser} from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { Chat, ChatSocket, ClientCam } from 'src/app/interfaces/chat.interface';
import { ChatService } from 'src/app/services/chat/chat.service';
import { ModelService } from 'src/app/services/model/model.service';
import { SocketService } from 'src/app/services/socket/socket.service';
import { RoomPrivateService } from '../../../services/roomPrivate/room-private.service';
import { PopupService } from '../../../services/popup/popup.service';
import { ProfilService } from '../../../services/profil/profil.service';
import { MatDialog } from '@angular/material/dialog';
import { PackModalComponent } from '../../../modals/pack-modal/pack-modal.component';
import { TimerService } from '../../../services/timer/timer.service';
import { TypeTimer } from 'src/app/interfaces/timer.interface';
import { ClientService } from '../../../services/client/client.service';
import { NotificationService } from '../../../services/notification/notification.service';
import { CreditTimer } from '../../../interfaces/timer.interface';
import { RoomVipService } from '../../../services/roomVip/room-vip.service';

// Peer
import './../../modele/shim';
import Peer from "peerjs"
import { Observable, Subscription } from 'rxjs';

@Component({
  selector: 'app-live-private',
  templateUrl: './live-private.component.html',
  styleUrls: ['./live-private.component.scss', './../live.scss']
})
export class LivePrivateComponent implements OnInit {
  context: string = "PRIVEE";
  imgPoster = "assets/images/image.png";
  thrombone = "assets/icons/paper-clip.svg";
  send = "assets/icons/send-button.svg";
  dot = "assets/icons/more_purple.svg";
  close = "assets/icons/close.svg";

  toggleDot: boolean = false;

  bg = "assets/logos/dark-light.jpg";
  modelPseudo = '';
  modelId: number;
  idRoom: number;
  actif: number;
  message: string = '';
  chatColor = "black";
  clientId: number;
  clientPseudo: string = null;
  clientCredit: number  = 0.

  messages: Chat[] = [];
  leaved: boolean = false;

  invitedVIP = false;
  passedVIP = false;
  roomVIP: string = null;

  clients: ClientCam[] = [];

  timer = {
    id: 0,
    hour: 0,
    minute: 0,
    second: 0,
    fail: false,
    reinit: false
  };

  show = {
    id: 0,
    credit: null,
    second: null,
    type: null
  };

  actifSub = new Subscription();
  joinSub = new Subscription();
  leaveSub = new Subscription();
  messageSub = new Subscription();
  modelLeaveSub = new Subscription();
  modelPassedVipSub = new Subscription();
  invitationVipSub = new Subscription();
  peerSub = new Subscription();

  // @HostListener("window:beforeunload", ["$event"]) unloadHandler(event: Event) {
  //   this.socketService.leavePrivate(this.idRoom, 'client');
  // }

    // WEB RTC VIDEO
    @ViewChild('video_live_model', {static: true}) remote_video: ElementRef<HTMLVideoElement>;
    @ViewChild('video_live_client', {static: true}) video: ElementRef<HTMLVideoElement>;

    private peer: Peer;
    peerIdShare: string;
    peerId: string;
    private lazyStream: any;
    currentPeer: any;
    private peerList: Array<any> = [];


    constructor(
    @Inject(PLATFORM_ID) private _platform: Object,
    private router: Router,
    private route: ActivatedRoute,
    private modelService: ModelService,
    private roomPrivateService: RoomPrivateService,
    private store: LocalStorageService,
    private chatService: ChatService,
    private socketService: SocketService,
    private popupService: PopupService,
    private profilService: ProfilService,
    private dialog: MatDialog,
    private timerService: TimerService,
    private clientService: ClientService,
    private notificationService: NotificationService,
    private roomVipService: RoomVipService
    ) {
      this.peer = new Peer();
      const id = this.route.snapshot.paramMap.get('model');
      this.modelId = parseInt(id);
    }

  ngOnInit(): void {
      if (!this.modelId) return null;
      this.getModel();
      this.initColor();
      this.clientService.lastRoom(this.router.url); // save last room for chat
  }

  async ngOnDestroy() {
    (await this.creditTimer()).subscribe(
      (data) => {
        // console.log("Response credit timer ", data);
      }
    );

    this.actifSub.unsubscribe();
    this.joinSub.unsubscribe();
    this.leaveSub.unsubscribe();
    this.messageSub.unsubscribe();
    this.modelLeaveSub.unsubscribe();
    this.modelPassedVipSub.unsubscribe();
    this.invitationVipSub.unsubscribe();
    this.peerSub.unsubscribe();
  }

  getModel() {
    this.modelService.getModel(this.modelId).subscribe(
      (data) => {
        // console.log("Le model ", data);
        this.bg = data.path_soft;
        this.modelPseudo = data.pseudo;
        if (data.profile.status === "En vip") {
          this.passedVIP = true;
          this.clearTimer();
          // return null;
        } else if (data.profile.status === "En ligne") {
          this.obsolete();
          this.clearTimer();
          // return null;
        }
        this.getInfo();
      }
    )
  }

  getInfo() {
    this.clientService.getMyInfos().subscribe( // My info
      (data) => {
        // console.log("My info ", data)
        this.clientId = data.id;
        this.clientPseudo = data.pseudo;
        this.clientCredit = data.credit ? data.credit.credit : 0; // Client Credit
        this.getInfoRoom();
      }
    )
  }

  async getCredit() {
    return await this.clientService.getCredit().subscribe(
      (data) => {
        this.clientCredit = data.credit;
        // console.log('Client credit ', this.clientCredit);
        if (this.show.credit < this.clientCredit) return null;
        this.timer.fail = true;
        // if Not leaved
        if (!this.timer.reinit) {
          this.notificationService.failure("CREDIT INSUFFISANT", "Veuillez vous recharger.");
        }
      }
    )
  }

  async getInfoRoom() {
    await this.roomPrivateService.getRoomModel(this.modelId).subscribe(
      async (data) => {
        // console.log('Info room ', data);
        this.idRoom = data.idRoom;

        if (data.idRoom === null) {
          this.obsolete();
          this.idRoom = 0;
          this.clearTimer();
        }
        this.getMessages();
        // console.log("Before initSocket");
        this.initSocket()
      }
    )
  }

  async getActifs() {
    await this.roomPrivateService.getActif(this.idRoom).subscribe(
      (data) => {
        // console.log("Get les actifs ", data);
        this.actif = data.clients.length + 1; // +1 for the model
        this.clients = data.clients;
      }
    )
  }

  getMessages() {
    this.chatService.getMessage(this.idRoom, 'private').subscribe(
      (data) => {
        this.messages = data;
      }
    )
  }

  async initSocket() {

    await this.socketService.joinPrivate(this.idRoom, this.modelId,
      this.clientId, this.clientPseudo);

    this.actifSub = await this.roomPrivateService.updateActif(this.idRoom, true, 'private').subscribe(
      (data) => {
        // console.log("Retour Update Actif in room Service ", data);
        this.getActifs();
      }
    );

    this.joinSub = this.socketService.listen(`joined ${this.idRoom}P`).subscribe(
      (data: any) => {
        // console.log('Joined ', data);
        this.getActifs();
      }
    );

    this.leaveSub = this.socketService.listen(`leaved ${this.idRoom}P`).subscribe(
      (data: any) => {
        // console.log('leaved ', data);
        this.getActifs();
      }
    );

    this.messageSub = this.socketService.listen(`message ${this.idRoom}P`).subscribe(
      (data) => {
        this.getMessages();
      }
    );

    // Model leaved the room
    this.modelLeaveSub = this.socketService.listen(`model leaved ${this.idRoom}P`).subscribe(
      (data) => {
        this.leaved = true;
        this.actif--;
        this.popupService.info('/accueil/registered','LIVE INDISPONIBLE',
        `${this.modelPseudo} a ferm le live de ce room`);
      }
    );

    // Model passed to VIP
    this.modelPassedVipSub = this.socketService.listen(`Pass to VIP ${this.idRoom}P`).subscribe(
      (data: any) => {
        this.passedVIP = true;
      }
    );

    // Invitation to pass in VIP
    this.invitationVipSub = this.socketService.listen(`pass VIP ${this.clientId} ${this.idRoom}P`).subscribe(
      (data: any) => {
        this.invitedVIP = true;
        this.invitationVIP();
        this.roomVIP = data.roomVIP;
        this.store.set("roomVIP", this.roomVIP);
      }
    );

    // Invitation to pass in VIP
    this.peerSub = this.socketService.listen(`ans peerId ${this.clientId} ${this.idRoom}P`).subscribe(
        (data: {peerId: any, room: string, clientId: number}) => {
            console.log("ans peerId ", data);
            this.peerIdShare = data.peerId;
            this.connectWithPeer();
      }
    );

    this.initTimer();
    this.initLiveVideo();
  }

  obsolete() {
    this.leaved = true;
    this.popupService.info('/accueil/registered','LIVE INDISPONIBLE',
    `Aucun live n\'est lanc dans ce room`);
  }

  initColor() {
    if (this.store.get("ticket_chat")) {
      this.chatColor = this.store.get("ticket_chat");
      return true;
    };
    this.roomPrivateService.getColor().subscribe(
      (data) => {
        this.chatColor = data.color;
        this.store.set("ticket_chat", data.color);
      }
    )
  }

  sendMessage() {
    if (!this.message) return;
    const data: Chat = {
      idRoom: this.idRoom,
      message: this.message,
      type_source: 'client',
      id_source: this.clientId,
      code_couleur: this.chatColor,
      type_chat: 'private'
    }

    this.chatService.postMessage(data).subscribe(
      (data) => {
        this.getMessages();
        const msg: ChatSocket = {
          room: this.idRoom + 'P',
          role: 'client',
          id: this.clientId,
          message: this.message
        }
        this.socketService.sendMessage(msg);
        this.message = null;
      }
    )
  }


  toggleDots() {
    this.toggleDot = !this.toggleDot;
  }

  async liveVIP() {
    return (await this.leaveTimer()).subscribe(
      async (data) => {
        this.timer.reinit = true;

        (await this.creditTimer()).subscribe(
          (data: { credit: number; id: number; updatedAt: Date; }) => {
            this.clientCredit = data.credit ? data.credit : 0;
            if (data.credit <= 0) {
              this.aucunCredit();
              return null;
            } else if (data.credit <= (this.show.credit * 2)) {
              this.creditInsuffisant();
              return null;
            }
            this.profilService.getSpecificProfil(this.modelId).subscribe(
              async (data) => {
                if (data.status === "En vip" && this.getInfoRoomVIP()) {
                  this.router.navigate(['/live/vip', { model: this.modelId }]);
                } else {
                  this.popupService.info(null, 'LIVE VIP INDISPONIBLE',
                    `${this.modelPseudo} ne vous a pas invit en VIP.`);
                }
              }
            );

          })
      }
    );
  }

  async getInfoRoomVIP() {
    return await this.roomVipService.getRoomModel(this.modelId).subscribe(
      (data) => {
        if (data.idRoom === null) {return false;}
        if (this.clientId !== data.clientId) {return false;}
        return true;
      })
  }

  async liveOut() {
    // this.creditTimer();
    this.onStop();
    await (await this.leaveTimer()).subscribe(
      async (data) => {
        this.timer.reinit = true;
        await this.roomPrivateService.updateActif(this.idRoom, false, 'private').subscribe(
          (data) => {
            this.socketService.leavePrivate(this.idRoom, 'client').then(
              (data) => {
                this.roomPrivateService.leaveRoom();
                this.router.navigate(['/accueil/registered']);
              }
            );
          }
        );
      })
  }

  buy() {
    const dialogRef = this.dialog.open(PackModalComponent, {
      width: '80vh',
      data: {}
    });
    dialogRef.afterClosed().subscribe(result => {});
  }

  invitationVIP() {
    this.popupService.info(null,'LIVE VIP',
    `${this.modelPseudo} vous a invit en LIVE VIP. Veuillez passer en show VIP`);
  }


  // Timer
  async initTimer() {
    this.timerService.getTimer(this.modelId, TypeTimer.PRIVATE).subscribe(
      async (data) => {
        // console.log('Timer ', data);
        const created: Date = data.createdAt;
        const updated: Date = data.updatedAt;
        this.timer.id = data.id;
        // console.log(created.toString(), " to ", updated.toString());
        const {hour, minute, second} = this.timerService.convertTime(created.toString(), updated.toString());
        this.timer.hour = hour;
        this.timer.minute = minute;
        this.timer.second = second;
        // console.log(this.timer.hour, ':', this.timer.minute, ':', this.timer.second);
        await this.getCostShow();
      }
    )
  }

  async getCostShow() {
    return await this.timerService.getCostShow(TypeTimer.PRIVATE).subscribe(
      (data: any) => {
        this.show.id = data.id;
        this.show.credit = data.credit;
        this.show.second = data.second;
        this.show.type = data.type;
        // console.log('Show cost ' , this.show, " - credit - " , this.clientCredit);
        if (this.clientCredit == 0) {
          this.aucunCredit();
          return null;
        } else if (this.clientCredit <= (this.show.credit * 2)) {
          this.creditInsuffisant();
          return null;
        }
        this.beginTimer();
      }
    )
  }

  creditInsuffisant() {
    this.popupService.info(null,'CREDIT INSUFFISANT',
    `Vous n\'avez presque plus de crdit. Veuillez vous recharger.`);
    setTimeout(
      () => {
        this.getCredit();
      }, 20000
    )
  }

  aucunCredit() {
    this.timer.fail = true;
    this.popupService.info('/achat_de_credit','ACHAT DE CREDIT',
    `Veuillez vous recharger.`);
  }

  // Timer set to 0
  clearTimer() {
    this.timer.reinit = true;
  }

  async leaveTimer(): Promise<Observable<any>> {
    const data = {
      id: this.timer.id,
      type: TypeTimer.PRIVATE,
      leaved: true,
      push: new Date().toString()
    }

    return await this.timerService.updateTimer(data);
  }

  async beginTimer() {
    const delay = this.show.second * 1000;
    // console.log(delay, " ms ");
    await (await this.creditTimer()).subscribe( // launch main creditation
      (data) => {
        // console.log("Response credit timer ", data);
        this.clientCredit = data.credit ? data.credit : 0;
        if (data.credit <= 0) {
          this.aucunCredit();
          return null;
        } else if (data.credit <= (this.show.credit * 2)) {
          this.creditInsuffisant();
          return null;
        }
      }
    );
    setInterval(
      async () => {
        if (!this.timer.reinit) {
          await (await this.creditTimer()).subscribe(
            (data) => {
              // console.log("Response credit timer ", data);
              this.clientCredit = data.credit ? data.credit : 0;
              if (data.credit <= 0) {
                this.aucunCredit();
                return null;
              } else if (data.credit <= (this.show.credit * 2)) {
                this.creditInsuffisant();
                return null;
              }
            }
          );
        }
      }, delay
    )
  }

  async creditTimer(): Promise<Observable<any>> {
    if ((this.clientId === undefined) || (this.show.credit == null)) return null;
    const data: CreditTimer = {
      clientId: this.clientId,
      modelId: this.modelId,
      timerId: this.timer.id,
      showCredit: this.show.credit,
      showSecond: this.show.second,
      showType: this.show.type,
      roomId: this.idRoom
    }
    // console.log("credit timer post ", data);
    return await this.timerService.creditTimer(data);
  }

    // WEB RTC - LIVE VIDEO
    initLiveVideo() {
      //this.onStart();
      this.getPeerId();
    }

    onStart(){
      if(isPlatformBrowser(this._platform) && 'mediaDevices' in navigator) {
        navigator.mediaDevices.getUserMedia({video: true}).then((ms: MediaStream) => {
          const _video = this.video.nativeElement;
          _video.srcObject = ms;
          _video.play();
        });
      }
    }

    onStop() {
      if (this.video && this.video.nativeElement.srcObject) {
        this.video.nativeElement.pause();
        if ((this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0]) {
          (this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0].stop();
        }
        this.video.nativeElement.srcObject = null;
      }
    }

    private getPeerId = () => {
      this.peer.on('open', (id) => {
        console.log("Peer Id ", id);
        this.peerId = id;
        // Send peer client
        this.socketService.askModelPeerId({peerId: this.peerId,
          room: this.idRoom+'P', clientId: this.clientId})
      });

      this.peer.on('call', (call) => {
        this.onStop();
        navigator.mediaDevices.getUserMedia({
          video: true,
          audio: true
        }).then((stream) => {
          this.lazyStream = stream;
          // const _video = this.video.nativeElement;
          // _video.srcObject = stream;
          // _video.play();

          call.answer(stream);
          call.on('stream', (remoteStream) => {
            if (!this.peerList.includes(call.peer)) {
              this.streamRemoteVideo(remoteStream);
              this.currentPeer = call.peerConnection;
              this.peerList.push(call.peer);
            }
          });
        }).catch(err => {
          console.log(err + ' - Unable to get media');
        });
      });
    }

    connectWithPeer(): void {
      this.callPeer(this.peerIdShare);
    }

    private callPeer(id: string): void {
      console.log("Client call model");
      navigator.mediaDevices.getUserMedia({
        video: true,
        audio: true
      }).then((stream) => {
        this.lazyStream = stream;
        console.log("Get client stream ", stream);
        const call = this.peer.call(id, stream);
        console.log("After call model");
        call.on('stream', (remoteStream) => {
          console.log("On stream in client");
          if (!this.peerList.includes(call.peer)) {
            this.streamRemoteVideo(remoteStream);
            this.currentPeer = call.peerConnection;
            this.peerList.push(call.peer);
          }
        });
      }).catch(err => {
        console.log(err + 'Unable to connect');
      });
    }

    private streamRemoteVideo(stream: any): void {
      const _video = this.remote_video.nativeElement;
      _video.srcObject = stream;
      _video.play();
      // const video = document.createElement('video');
      // video.classList.add('video');
      // video.srcObject = stream;
      // video.play();
      // document.getElementById('remote-video').append(video);
    }


}
