import { EncryptionService } from './../../../services/encryption/encryption.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import * as $ from 'jquery';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/services/auth/auth.service';
import { loginUser } from 'src/app/interfaces/login.interface';
import { environment } from 'src/environments/environment';
import { LogService } from '../../../services/log/log.service';
import { Log } from '../../../interfaces/log.interface';
import { RoomService } from '../../../services/room/room.service';
import { LocalStorageService } from 'ngx-localstorage';

@Component({
  selector: 'app-connexion-model',
  templateUrl: './connexion-model.component.html',
  styleUrls: ['./connexion-model.component.scss']
})
export class ConnexionModelComponent implements OnInit {
  loginForm : FormGroup;

  submitted :boolean =  false;
  loading: boolean = false;

  checked :boolean =  false;

  pseudoStorage : string = null;
  passStorage : string = null;
  errorAuth  :boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private encryptionService: EncryptionService,
    private authService: AuthService,
    private router: Router,
    private logService: LogService,
    private roomService: RoomService,
    private store: LocalStorageService
  ) {
    this.loginForm = this.formBuilder.group({
      pseudo: ['', Validators.required],
      password: ['', Validators.required]
    })
  }

  ngOnInit(): void {
    this.isRemembered();
  }

  get form() { return this.loginForm.controls }

  remember(value) {
    if (value.target.checked) {
        this.checked = true;
        localStorage.setItem("checklogmod", "true");
    } else {
        this.checked = false;
        localStorage.setItem("checklogmod", "false");
    }
  }

  async isRemember(user: loginUser) {
    if (this.checked) {
        const pseudoCrypted = this.encryptionService.encryptUsingAES256(user.pseudo);
        const passCrypted = this.encryptionService.encryptUsingAES256(user.password);
        localStorage.setItem(environment.CRYPT_PSEUDO_MODEL, pseudoCrypted);
        localStorage.setItem(environment.CRYPT_PASS_MODEL, passCrypted);
    } else {
        localStorage.setItem(environment.CRYPT_PSEUDO_MODEL, null);
        localStorage.setItem(environment.CRYPT_PASS_MODEL, null);
    }
  }

  isRemembered() {
    const pseudoStorage = localStorage.getItem(environment.CRYPT_PSEUDO_MODEL) ? localStorage.getItem(environment.CRYPT_PSEUDO_MODEL) : null;
    const passStorage = localStorage.getItem(environment.CRYPT_PASS_MODEL) ? localStorage.getItem(environment.CRYPT_PASS_MODEL) : null;

    if (pseudoStorage && passStorage) {
      this.pseudoStorage = this.encryptionService.decryptUsingAES256(pseudoStorage);
      this.passStorage = this.encryptionService.decryptUsingAES256(passStorage);

      this.pseudoStorage = this.pseudoStorage.replace(/['"]+/g, '');
      this.passStorage = this.passStorage.replace(/['"]+/g, '');

      this.loginForm.setValue({
            pseudo: this.pseudoStorage,
            password: this.passStorage
      })
    }

    if (localStorage.getItem("checklogmod") === "true")
      $('#checker').prop('checked', true);
    else
      $('#checker').prop('checked', false);
  }

  async login(event) {
    this.submitted = true;
    event.preventDefault();
    if (this.loginForm.invalid) {return false;}
    this.loading = true;
    let user: loginUser = this.loginForm.value;
    await this.authService.loginModel(user).subscribe(
      (res: any) => {
          // console.log(res);
          if (res.error) {
            this.errorAuth = res.message;
          } else {
            this.errorAuth = null;
            if (res.access_token)
              this.authService.saveToken(res.access_token).then(
                () => {
                  this.logService.createLog("connection").subscribe(
                    (data) => {
                      this.isRemember(user).then(
                        () => {
                          this.roomService.createRoom().subscribe(
                            (data) => {
                              this.store.set("room", data.room);
                              this.router.navigate(['/modele/profile']);
                            }
                          )
                        }
                      );
                    }, (error) => {
                      console.log(error);
                    }
                  );

                }
              );
          }
          this.loading = false;
      },
      (err: any) => {
          this.loading = false;
          console.log(err);
      }
    );

    return true;
  }

  saveLog() {
    this.logService.createLog("connection").subscribe(
      (data) => {
        console.log('Log ', data)
      }, (error) => {
        console.log(error);
      }
    );
  }

}
