import { Injectable } from '@angular/core';
import { addAlbum } from '../../interfaces/add-album.interface';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { SpecificAlbum } from '../../interfaces/specific-album.interface';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Injectable({
  providedIn: 'root'
})
export class AlbumService {
  endpoint = environment.endpoint


  public albumPrivate = [
    'assets/images/image.png', 'assets/images/bg.jpg',
    'assets/images/image.png', 'assets/images/image.png',
    'assets/images/image.png', 'assets/images/image.png',
    'assets/images/bg.jpg', 'assets/images/image.png',
    'assets/images/image.png', 'assets/images/bg.jpg',
    'assets/images/bg.jpg', 'assets/images/bg.jpg',
    'assets/images/bg.jpg', 'assets/images/image.png',
    'assets/images/bg.jpg', 'assets/images/image.png',
    'assets/images/bg.jpg', 'assets/images/bg.jpg',
    'assets/images/bg.jpg', 'assets/images/bg.jpg',
    'assets/images/bg.jpg', 'assets/images/bg.jpg'
  ];

  public albumVIP = [
    'assets/images/image2.jpg', 'assets/images/bg.jpg',
    'assets/images/image2.jpg', 'assets/images/image.png',
    'assets/images/image2.jpg', 'assets/images/image.png',
    'assets/images/bg.jpg', 'assets/images/image.png',
    'assets/images/image2.jpg', 'assets/images/image2.jpg',
    'assets/images/bg.jpg', 'assets/images/bg.jpg',
    'assets/images/bg.jpg', 'assets/images/image2.jpg',
    'assets/images/bg.jpg', 'assets/images/image2.jpg',
    'assets/images/bg.jpg', 'assets/images/bg.jpg',,
    'assets/images/image2.jpg', 'assets/images/bg.jpg'
  ];

  public albumFree = [
    'assets/images/image3.jpg', 'assets/images/image3.jpg',
    'assets/images/image.png', 'assets/images/image.png',
    'assets/images/bg.jpg', 'assets/images/image3.jpg',
    'assets/images/image3.jpg', 'assets/images/bg.jpg',
    'assets/images/bg.jpg', 'assets/images/bg.jpg',
    'assets/images/bg.jpg', 'assets/images/image.png',
    'assets/images/image3.jpg', 'assets/images/image.png',
    'assets/images/bg.jpg', 'assets/images/bg.jpg',
    'assets/images/image3.jpg', 'assets/images/bg.jpg'
  ];


  constructor(
    private http: HttpClient
  ) { }

  totalVip() {
    return this.albumVIP.length;
  }

  totalFree() {
    return this.albumFree.length;
  }

  totalPrivate() {
    return this.albumPrivate.length;
  }

  async addPhoto(url: string, file: any, context: string) {
    console.log(url);
    console.log(context);
    var tmp = [];
    var tab: any;
    tmp.push(url);
    switch (context) {
      case 'private':
      for (let index = 0; index < this.albumPrivate.length; index++) {
        tmp.push(this.albumPrivate[index]);
      }
      this.albumPrivate = tmp;
      break;

      case 'vip':
        for (let index = 0; index < this.albumVIP.length; index++) {
          tmp.push(this.albumVIP[index]);
        }
        this.albumVIP = tmp;
        break;

      case 'free':
        for (let index = 0; index < this.albumFree.length; index++) {
          tmp.push(this.albumFree[index]);
        }
        this.albumFree = tmp;
        break;

      default:
        break;
    }
  }

  async getAlbums(context: string, debut: number, marge: number): Promise<Observable<any>> {
    const album: SpecificAlbum = {
      type_album: context,
      begin: debut,
      end: debut + marge
    }
    return this.http.post(`${this.endpoint}/album/specific`, album, httpOptions)
  }

  uploadAlbumModel(formData: FormData): Observable<any> {
    return this.http.post(`${this.endpoint}/upload/album`,formData,{
        reportProgress: true,
        observe: 'events'
    });
  }

  addAlbums(albums: addAlbum[]): Observable<any> {
    return this.http.post(`${this.endpoint}/album`, albums, httpOptions)
  }

  getCount(): Observable<any> {
    return this.http.get(`${this.endpoint}/album/model/count`, httpOptions)
  }

  // Get model albums by client
  getModelAlbums(data: {modelId: number, type_album: string}) {
    return this.http.post(`${this.endpoint}/album/getAlbums`, data, httpOptions)
  }

  deleteAlbum(id: number) {
    return this.http.delete(`${this.endpoint}/album/${id}`, httpOptions)
  }

}
