import { DeleteComponent } from './delete/delete.component';
import { PasswordComponent } from './password/password.component';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ClientService } from '../../../services/client/client.service';
import { PackModalComponent } from 'src/app/modals/pack-modal/pack-modal.component';
import { NotificationService } from '../../../services/notification/notification.service';
import { AuthService } from '../../../services/auth/auth.service';

@Component({
  selector: 'app-profil',
  templateUrl: './profil.component.html',
  styleUrls: ['./profil.component.scss']
})
export class ProfilComponent implements OnInit {
  credit: number = 500;
  sound: boolean = false;
  autoRecharge = false;
  coinImg = "assets/icons/coin-stack.svg";
  exclamation = "assets/icons/exclamation-mark.svg";
  // Switch
  color = "#FB77FC";
  disabled = false;

  // Modification information
  isModifing = false;
  showExclam = false;

  // Information
  email = "ohatra@yahoo.fr";
  pseudo = "Cactus";

  info = {
    clientId: 0,
    clientPseudo: null,
    clientEmail: null,
    clientCredit: 0,
    clientCreditId: 0
  }

  constructor(
    private dialog: MatDialog,
    private clientService: ClientService,
    private notificationService: NotificationService,
    private authService: AuthService) {
  }

  ngOnInit(): void {
    this.getInfo();
  }

  getInfo() {
    this.clientService.getMyInfos().subscribe(
      (data) => {
        // console.log("My info ", data);
        if (!data) return null;
        this.info.clientId = data.id;
        this.info.clientPseudo = data.pseudo;
        this.info.clientEmail = data.email;
        this.info.clientCredit = data.credit.credit;
        this.info.clientCreditId = data.credit.id;
      }
    )
  }

  recharge() {
    const dialogRef = this.dialog.open(PackModalComponent, {
      width: '100vh',
      data: {}
    });
    dialogRef.afterClosed().subscribe(result => {
      this.getInfo();
    });
  }


  changeSound() {
    this.sound = !this.sound;
    console.log(this.sound);
  }

  toogleRecharge(event) {
    console.log(event.checked);
    this.autoRecharge = event.checked;
  }

  modifing () {
    if (this.isModifing) {
      // console.log("New Pseudo ", this.info.clientPseudo);
      const newPseudo = this.info.clientPseudo;
      this.clientService.changePseudo(newPseudo).subscribe(
        (data: any) => {
          // console.log(data);
          if (data.access_token) {
            this.authService.saveToken(data.access_token, data.id).then(
              () => {
                this.notificationService.success("Pseudo modifi", "");
              }
            );
          }
        }
      )
    }
    this.isModifing = !this.isModifing;
  }

  tryChangeEmail() {
    if (this.isModifing) {
      this.showExclam = true;
      setTimeout(() => {
        this.showExclam = false;
      }, 4000);
    }
  }

  openDialogPassword() {
    const dialogRef = this.dialog.open(PasswordComponent, {
      width: '50vh'
    });
    dialogRef.afterClosed().subscribe(result => {
      // console.log(`Dialog result: ${result}`);
    });
  }

  deleteAccount(action: string): void {
    const dialogRef = this.dialog.open(DeleteComponent, {
      width: '50vh',
      data: {action: action}
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log('The dialog was closed');
    });
  }
}
