import { Component, OnInit,
  OnDestroy, PLATFORM_ID,
  Inject, ViewChild,
  ElementRef } from '@angular/core';
import {isPlatformBrowser} from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { LocalStorageService } from 'ngx-localstorage';
import { Chat, ChatSocket, ClientCam } from 'src/app/interfaces/chat.interface';
import { ChatService } from 'src/app/services/chat/chat.service';
import { ModelService } from 'src/app/services/model/model.service';
import { SocketService } from 'src/app/services/socket/socket.service';
import { RoomPrivateService } from '../../../services/roomPrivate/room-private.service';
import { PopupService } from '../../../services/popup/popup.service';
import { ProfilService } from '../../../services/profil/profil.service';
import { RoomVipService } from '../../../services/roomVip/room-vip.service';
import { CreditService } from '../../../services/credit/credit.service';
import { TimerService } from '../../../services/timer/timer.service';
import { TypeTimer } from 'src/app/interfaces/timer.interface';
import * as $ from 'jquery';

// For Peer
import './../shim';
import Peer from "peerjs"
import { Subscription, Observable } from 'rxjs';

@Component({
  selector: 'app-live-private-model',
  templateUrl: './live-private-model.component.html',
  styleUrls: ['./live-private-model.component.scss' , './../../live/live.scss']
})
export class LivePrivateModelComponent implements OnInit {
  context: string = "LIVE PRIVEE";
  imgPoster = "assets/images/image.png";
  thrombone = "assets/icons/paper-clip.svg";
  send = "assets/icons/send-button.svg";
  dot = "assets/icons/more_purple.svg";
  close = "assets/icons/close.svg";

  toggleDot: boolean = false;

  info = {
    bg : "assets/logos/dark-light.jpg",
    message : '',
    idRoom: 0,
    pseudo: '',
    chatColor:'#FB77FC',
    modelId: 0,
    actif: 0,
    creditModel: 0,
    idCreditModel: 0
  }

  timer = {
    id: 0,
    hour: 0,
    minute: 0,
    second: 0,
    fail: false,
    reinit: false
  };

  messages: Chat[] = [];
  leaved: boolean = false;

  clientId: number = null;

  clients: ClientCam[] = [];

  joinSub = new Subscription();
  leaveSub = new Subscription();
  messageSub = new Subscription();
  peerSub = new Subscription();

  stat = {
    id_show: 0,
    type_show : null,
    tarif_show: 0,
    time_show: 0,
    time_passed : this.timer.hour + ':' + this.timer.minute + ':' + this.timer.second,
    hour_passed : null,
    minute_passed : null,
    second_passed : null,
    actualCredit: this.info.creditModel,
    winCredit: 0,
    totalCredit: this.info.creditModel
  }

  // WEB RTC VIDEO
  @ViewChild('video_live_model', {static: true}) video: ElementRef<HTMLVideoElement>;
  private peer: Peer;
  peerIdShare: string;
  peerId: string;
  private lazyStream: any;
  currentPeer: any;
  private peerList: Array<any> = [];

  constructor(
    @Inject(PLATFORM_ID) private _platform: Object,
    private router: Router,
    private store: LocalStorageService,
    private modelService: ModelService,
    private chatService: ChatService,
    private roomPrivateService: RoomPrivateService,
    private roomVipService: RoomVipService,
    private socketService: SocketService,
    private profilService: ProfilService,
    private popupService: PopupService,
    private creditService: CreditService,
    private timerService: TimerService) {
      this.peer = new Peer();
  }

  ngOnInit(): void {
    this.getInfo();
    this.initLiveVideo();
  }

  ngOnDestroy(): void {
    this.joinSub.unsubscribe();
    this.leaveSub.unsubscribe();
    this.messageSub.unsubscribe();
    this.peerSub.unsubscribe();
  }

  getInfo() {
    this.modelService.getInfo().subscribe(
      (data) => {
        // console.log('Get info ', data);
        this.info.bg = data.path_soft;
        this.info.modelId = data.id;
        this.info.pseudo = data.pseudo;
        this.getInfoRoom();
      }
    )
  }

  getInfoRoom() {
    this.roomPrivateService.getRoomModel(this.info.modelId).subscribe(
      async (data) => {
        // console.log('Model room ', data);
        this.info.idRoom = data.idRoom;
        this.getCredit();
        this.initSocket();
      }
    )
  }

  async getActifs() {
    await this.roomPrivateService.getActif(this.info.idRoom).subscribe(
      (data) => {
        // console.log("Get les actifs ", data);
        this.info.actif = data.clients.length;
        this.clients = data.clients;
      }
    )
  }

  getCredit() {
    this.creditService.getCredit().subscribe(
      (data) => {
        this.info.creditModel = data ? data.credit : 0;
        this.info.idCreditModel = data ? data.id : 0;
      }
    );

    // Launch the timer
    this.initTimer();
  }

  initSocket() {
    this.getMessages();

    this.joinSub = this.socketService.listen(`joined ${this.info.idRoom}P`).subscribe(
      (data: any) => {
        // console.log('joined ', data);
        this.getActifs();
      }
    );

    this.leaveSub = this.socketService.listen(`leaved ${this.info.idRoom}P`).subscribe(
      (data: any) => {
        // console.log('leaved ', data);
        this.getActifs();
      }
    );

    this.messageSub = this.socketService.listen(`message ${this.info.idRoom}P`).subscribe(
      (data) => {
        this.getMessages();
      }
    );

    this.peerSub = this.socketService.listen(`ask peerId ${this.info.idRoom}P`).subscribe(
      async (data: {peerId: any, room: string, clientId: number}) => {
        console.log('Ask peer Id ', data);
        if (!this.peerId) await this.getPeerId();
        this.socketService.ansModelPeerId({
          peerId: this.peerId, room: this.info.idRoom+'P', clientId: data.clientId
        });
      }
    );

    this.getActifs();
  }

  getMessages() {
    this.chatService.getMessage(this.info.idRoom, 'private').subscribe(
      (data) => {
        // console.log('Messages ', data);
        this.messages = data;
      }
    )
  }

  toggleDots() {
    this.toggleDot = !this.toggleDot;
    // console.log(this.toggleDot);
  }

  sendMessage() {
    if (!this.info.message) return;

    const data: Chat = {
      idRoom: this.info.idRoom,
      message: this.info.message,
      type_source: 'model',
      id_source: this.info.modelId,
      type_chat: 'private',
      code_couleur: this.info.chatColor
    }

    this.chatService.postMessage(data).subscribe(
      (data) => {
        this.getMessages();
        const msg: ChatSocket = {
          room: this.info.idRoom + 'P',
          role: 'model',
          id: this.info.modelId,
          message: this.info.message
        }
        this.socketService.sendMessage(msg);
        this.info.message = null;
      }
    )
  }

  async liveVIP() {
    if (this.clientId) {
      this.leaved = true;
      this.clearTimer();
      (await this.leaveTimer()).subscribe(
        (data) => {
          this.timer.reinit = true;
          this.roomVipService.createRoom(this.clientId).subscribe(
            (data) => {
              this.store.set("vipRoomId", data.room);

              const msg = {
                room: this.info.idRoom + 'P',
                role: 'model',
                clientId: this.clientId,
                message: 'Let\'s go to VIP',
                roomVIP: data.room + 'V'
              }

              this.askLeave().then(
                (data) => {
                  this.socketService.emit('Invitation to VIP', msg).then(
                    (data) => {
                      // this.router.navigate(['/modele/live-vip', {clientId: this.clientId}]);
                      this.router.navigate(['/modele/live-vip']);
                    }
                  );
                }
              )
            }
          )
      })

    } else {
      this.popupService.info(null,'LIVE VIP',
      `Veuillez choisir votre client`);
    }
  }

  async askLeave() {
    const data = {
      room: this.info.idRoom + 'P',
      role: 'model',
      id: this.info.modelId,
      pseudo: this.info.pseudo,
      message: 'I pass to live VIP'
    }
    return await this.socketService.emit('Pass to VIP', data);
  }

  async liveOut() {
    // const room = this.store.get('privateRoomId');
    this.onStop();
    this.leaved = true;
    await (await this.leaveTimer()).subscribe(
      (data) => {
        this.timer.reinit = true;
        this.clearTimer(); // set to zero
        this.socketService.leavePrivate(this.info.idRoom, 'model');
        this.roomPrivateService.leaveRoom();
        this.profilService.updateStatus(this.info.modelId, 'En ligne').subscribe(
          (data) => {
            this.router.navigate(['/modele/chat']);
          }
        )
      }
    );
  }

  chooseClient(id: number) {
    this.clientId = id;
    // console.log('Selected client ', this.clientId);
  }

  // Timer
  async initTimer() {
      this.timerService.beginTimerModel(this.info.modelId, TypeTimer.PRIVATE).subscribe(
        async (data) => {
          // console.log('Timer ', data);
          const created: Date = data.createdAt;
          const updated: Date = data.updatedAt;
          this.timer.id = data.id;
          // console.log(created.toString(), " to ", updated.toString());
          const {hour, minute, second} = this.timerService.convertTime(created.toString(), updated.toString());
          this.timer.hour = hour;
          this.timer.minute = minute;
          this.timer.second = second;
          // console.log(this.timer.hour, ':', this.timer.minute, ':', this.timer.second);

          this.getCostShow();
        }
      )
  }

  async getCostShow() {
    return await this.timerService.getCostShow(TypeTimer.VIP).subscribe(
      (data: any) => {
        // console.log("Show Cost ", data)
        this.stat.tarif_show = data.credit? data.credit : 0;
        this.stat.time_show = data.second ? data.second : 0;
        this.stat.type_show = (data.type + '').toString().toUpperCase();

        this.beginCountCredit(this.stat.time_show);
      }
    )
  }

  beginCountCredit(nbSecond: number) {
    const delay = nbSecond * 1000;
    setInterval(
      () => {
        if (this.info.actif > 0) {
          this.creditService.getCredit().subscribe(
            (data) => {
              this.info.creditModel = data ? data.credit : 0;
            }
          );
        }
      }, delay
    )
  }

  // Timer set to 0
  clearTimer() {
    this.timer.reinit = true;
  }

  async leaveTimer(): Promise<Observable<any>> {
    const data = {
      id: this.timer.id,
      type: TypeTimer.PRIVATE,
      leaved: true,
      push: new Date().toString()
    }

    return await this.timerService.updateTimer(data);
  }

    // WEB RTC - LIVE VIDEO
    initLiveVideo() {
      this.onStart();
      this.getPeerId();
    }

    onStart(){
      if(isPlatformBrowser(this._platform) && 'mediaDevices' in navigator) {
        navigator.mediaDevices.getUserMedia({video: true}).then((ms: MediaStream) => {
          const _video = this.video.nativeElement;
          _video.srcObject = ms;
          _video.play();
        });
      }
    }

    onStop() {
      // let i = 0;
      // (this.video.nativeElement.srcObject as MediaStream).getVideoTracks().forEach(
      //   (stream) => {
      //     (this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[i].stop();
      //     i++;
      //   }
      // )

      if (this.video && this.video.nativeElement.srcObject) {
        this.video.nativeElement.pause();
        if ((this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0]) {
          (this.video.nativeElement.srcObject as MediaStream).getVideoTracks()[0].stop();
        }
        this.video.nativeElement.srcObject = null;
      }
    }

  private getPeerId = () => {
    this.peer.on('open', (id) => {
      console.log("Peer Id ", id);
      this.peerId = id;
    });

    this.peer.on('call', (call) => {
      this.onStop();
      console.log("Someone call model");
      navigator.mediaDevices.getUserMedia({
        video: true,
        audio: true
      }).then((stream) => {
        this.lazyStream = stream;
        const _video = this.video.nativeElement;
        _video.srcObject = stream;
        _video.play();
        console.log("Get model stream ", stream);
        call.answer(stream);
        console.log("After answer stream");
        call.on('stream', (remoteStream) => {
          console.log("On stream model");
          if (!this.peerList.includes(call.peer)) {
            this.streamRemoteVideo(remoteStream);
            this.currentPeer = call.peerConnection;
            this.peerList.push(call.peer);
          }
          console.log("Peer list ", this.peerList);
        });
      }).catch(err => {
        console.log(err + ' - Unable to get media');
      });
    });
  }

  connectWithPeer(): void {
    this.callPeer(this.peerIdShare);
  }

  private callPeer(id: string): void {
    navigator.mediaDevices.getUserMedia({
      video: true,
      audio: true
    }).then((stream) => {
      this.lazyStream = stream;

      const call = this.peer.call(id, stream);
      call.on('stream', (remoteStream) => {
        if (!this.peerList.includes(call.peer)) {
          this.streamRemoteVideo(remoteStream);
          this.currentPeer = call.peerConnection;
          this.peerList.push(call.peer);
        }
      });
    }).catch(err => {
      console.log(err + 'Unable to connect');
    });
  }

  private streamRemoteVideo(stream: any): void {
    // const video = document.createElement('video');
    // video.classList.add('video');
    // video.srcObject = stream;
    // video.play();

    // document.getElementById('remote-video').append(video);
    this.clients[0].client.stream = stream;
  }

}
