import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { LocalStorageService } from 'ngx-localstorage';
import { NotificationService } from '../../../services/notification/notification.service';


const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json'})
};

@Component({
  selector: 'app-confirm-email',
  templateUrl: './confirm-email.component.html',
  styleUrls: ['./confirm-email.component.scss']
})
export class ConfirmEmailComponent implements OnInit {
  endpoint = environment.endpoint

  token = null;
  errorToken: boolean = false;
  errorMessageToken: string = null;
  successMessageToken: string = null;

  constructor(
    private activatedRoute: ActivatedRoute,
    private http: HttpClient,
    private store: LocalStorageService,
    private router: Router,
    private notificationService: NotificationService
    ) {
    this.activatedRoute.queryParams.subscribe(params => {
          this.token = params['token'];
      });
  }

  ngOnInit(): void {
    this.confirmToken();
  }

  confirmToken() {
    if (!this.token) {
      this.errorToken = true;
      this.errorMessageToken = "Token introuvable"
      return false;
    }
    this.store.set("token", this.token);

    // POST client/confirm
    this.http.post(`${this.endpoint}/client/confirm`,{}, httpOptions).subscribe(
      (data: {success: boolean, message: string}) => {
        // console.log("Client token ", data);
        if (!data.success) {
          this.errorMessageToken = data.message;
          this.errorToken = true;
        } else {
          this.errorToken = false;
          this.successMessageToken = data.message;
          this.notificationService.infoMsg("Votre compte est confirm");
          this.router.navigate(['/connexion']);
        }
      }
    )
  }

}
